import React, {useEffect, useRef, useState, useMemo} from 'react';
import KeyCode from "../../utils/key-code";
import * as S from "./review-modal.styled";
import searchIcon from "../../assets/svg/icon-search.svg";
import searchClearIcon from "../../assets/svg/close.svg";
import NoteDropdown from "./note-dropdown";
import API from "../../services";


function ReviewModal(props) {
  const {
    content,
    open = false,
    data = [],
    users = [],
    noteRange = [],
    customOptions,
    onConfirm = (text) => text,
    onCancel = () => {
    },
    value = "",
    btnConfirmTextColor = "#fff",
    primaryColor = "#000",
    labels = {},
    closeIcon,
    expandRightIcon,
    expandLeftIcon,
    updateLocalStudentInteraction = (interaction, selectedUser) => {
    },
  } = props;
  const closeRef = useRef();
  const questionListRef = useRef();
  const [text, setText] = useState(value);
  const [searchText, setSearchText] = useState("");
  const [selectedUser, setSelectedUser] = useState({});
  const [isListOfItemsOpened, setIsListOfItemsOpened] = useState(false);
  const [questions, setQuestions] = useState(data);
  const [isLoadingNote, setIsLoadingNote] = useState([]);
  const [sortAnsweredOrder, setSortAnsweredOrder] = useState("");
  const [sortReviewedOrder, setSortReviewedOrder] = useState("");

  const byAnswered = (order = "asc") => (a, b) => {
    if (order === "asc") {
      if (a.answered && !b.answered) {
        return -1;
      }
      if (!a.answered && b.answered) {
        return 1;
      }
    } else if (order === "desc") {
      if (!a.answered && b.answered) {
        return -1;
      }
      if (a.answered && !b.answered) {
        return 1;
      }
    }
    return 0;
  }

  const byReviewed = (order = "asc") => (a, b) => {
    if (order === "asc") {
      if (a.reviewed && !b.reviewed) {
        return -1;
      }
      if (!a.reviewed && b.reviewed) {
        return 1;
      }
    } else if (order === "desc") {
      if (!a.reviewed && b.reviewed) {
        return -1;
      }
      if (a.reviewed && !b.reviewed) {
        return 1;
      }
    }
    return 0;
  }

  const handleSortByAnswered = () => {
    if (sortAnsweredOrder === "asc") {
      setSortAnsweredOrder("desc");
    } else if (sortAnsweredOrder === "desc") {
      setSortAnsweredOrder("");
    } else {
      setSortAnsweredOrder("asc");
    }
  }

  const handleSortByReviewed = () => {
    if (sortReviewedOrder === "asc") {
      setSortReviewedOrder("desc");
    } else if (sortReviewedOrder === "desc") {
      setSortReviewedOrder("");
    } else {
      setSortReviewedOrder("asc");
    }
  }

  const getStyleForOrder = (order) => {
    if (order === "asc") {
      return "overline"
    }
    if (order === "desc") {
      return "underline"
    }
    return "none"
  }

  const interactionObject = (user = {}, index) => {
    const {
      user_uid = index,
      name = `Usuário ${index + 1}`,
      email = "",
      interaction_objects = [],
      interaction_uid = "",
    } = user;

    let count_reviewed = 0;
    let answered = false;

    let interactionObjectTemp = questions.map(question => {
      let interaction = {};

      if (interaction_objects) {
        interaction = interaction_objects.find(interaction => interaction.target_uid === question.id);
        let isReviewed = false;

        if (interaction) {
          const {
            uid = null,
            reviewer_uid = null,
            object_data,
            score = null,
            target_uid = null,
            context_uid = null,
          } = interaction;

          const {
            data = ""
          } = object_data;

          if (object_data) {
            answered = true;
          }

          if (((score !== null && score !== "" && score !== "null") || reviewer_uid !== null)) {
            isReviewed = true;
            count_reviewed++;
          }
          return {
            uid,
            context_uid,
            reviewed: isReviewed,
            answered,
            score,
            answer: data,
            target_uid,
          }
        }
      }

      return {
        reviewed: false,
        answered,
        score: 0,
        answer: "",
        target_uid: null,
      }
    })

    return {
      uid: user_uid,
      name,
      email,
      interaction_uid,
      count_reviewed,
      total_messages: questions.length,
      reviewed: count_reviewed === questions.length,
      answered,
      interaction_objects: interactionObjectTemp,
    }
  }

  const updateLocalQuestion = (interaction) => {
    if (Array.isArray(interaction)) {
      let questionsCopy = [...questions];
      questionsCopy.forEach(question => {
        const userInteractionObject = interaction.find(item => item.target_uid === question.id);

        if (userInteractionObject) {
          question.score = userInteractionObject.score;
        } else {
          question.score = 0;
        }
      });

      setQuestions(questionsCopy);
    } else {
      let questionsCopy = [...questions];
      questionsCopy.forEach(question => {
        if (question.id === interaction.target_uid) {
          question.score = interaction.score;
        } else {
          question.score = 0;
        }
      });
      setQuestions(questionsCopy);
    }
    return interaction;
  }

  const students = useMemo(() => {
      const orderedUsers = users.map(interactionObject)
        .sort(byAnswered(sortAnsweredOrder))
        .sort(byReviewed(sortReviewedOrder));

      // Atualiza o usuário selecionado para refletir a mudança na nota
      if ("interaction_objects" in selectedUser) {
        const selectedUserUpdated = users.find(user => {
          return user.user_uid === selectedUser.uid
        });
        if (selectedUserUpdated !== undefined) {
          updateLocalQuestion(selectedUserUpdated.interaction_objects);
        }
      }

      return orderedUsers;
    },
    [users, sortAnsweredOrder, sortReviewedOrder]
  );

  useEffect(() => {
    if (questionListRef && questionListRef.current) {
      questionListRef.current.scrollTop = 0;
    }
    if (closeRef && closeRef.current) {
      closeRef.current.focus();
    }
    setSelectedUser({});
  }, [open]);

  const handleEscape = (event) => {
    let key = event.which || event.keyCode;

    if (key === KeyCode.ESC) {
      event.stopPropagation();
      onCancel();
    }
  }

  const handleSubmit = event => {
    event.preventDefault();
    onConfirm(text);
    setText("");
  }

  const handleClose = () => {
    onCancel();
    setText("");
  }

  const handleStudentSelect = (user) => {
    setSelectedUser(user);
    updateLocalQuestion(user.interaction_objects);
    setIsListOfItemsOpened(false);
    if (questionListRef && questionListRef.current) {
      questionListRef.current.scrollTop = 0;
    }
  }

  const handleStudentsMenu = () => {
    setIsListOfItemsOpened(!isListOfItemsOpened);
  }

  const handleSelectNote = (question, note) => {
    const {
      interaction_objects = [],
      interaction_uid,
    } = selectedUser;
    const userInteractionObject = interaction_objects.find(item => item.target_uid === question.id);

    if (userInteractionObject) {
      setIsLoadingNote([...isLoadingNote, question.id]);
      API.cms
        .learning
        .interaction.uid(interaction_uid)
        .score
        .object.uid(userInteractionObject.uid)
        .updateScore(note, customOptions)
        .then(updateLocalQuestion)
        .then(interaction => updateLocalStudentInteraction(interaction, selectedUser))
        .then(() => {
          setIsLoadingNote(isLoadingNote.filter(item => item !== question.id));
        })
    } else {
      setIsLoadingNote([...isLoadingNote, question.id]);

      API.cms.learning.interaction
        .uid(interaction_uid).object
        .reviewed({
          context_uid: content.id,
          type: content.type,
          target_uid: question.id,
          object_data: "",
          score: note,
        }, customOptions)
        .then(updateLocalQuestion)
        .then(interaction => updateLocalStudentInteraction(interaction, selectedUser))
        .then(() => {
          setIsLoadingNote(isLoadingNote.filter(item => item !== question.id));
        })
    }
  }

  const iconExpandLeft = () => {
    return expandRightIcon || (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        version="1.1"
        width="20"
        height="20"
        viewBox="0 0 24 24"
      >
        <path d="M4,2H2V22H4V13H18.17L12.67,18.5L14.08,19.92L22,12L14.08,4.08L12.67,5.5L18.17,11H4V2Z"/>
      </svg>
    )
  }

  const iconExpandRight = () => {
    return expandLeftIcon || (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        version="1.1"
        width="20"
        height="20"
        viewBox="0 0 24 24"
      >
        <path d="M20,22H22V2H20V11H5.83L11.33,5.5L9.92,4.08L2,12L9.92,19.92L11.33,18.5L5.83,13H20V22Z"/>
      </svg>
    )
  }

  const iconClose = () => {
    return closeIcon || (
      <svg
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        width="20"
        height="20"
        viewBox="0 0 768 768">
        <path
          d="M607.5 205.5l-178.5 178.5 178.5 178.5-45 45-178.5-178.5-178.5 178.5-45-45 178.5-178.5-178.5-178.5 45-45 178.5 178.5 178.5-178.5z"/>
      </svg>
    )
  }

  const handleSearch = (event) => {
    const {value} = event.target;
    setSearchText(value);
  }

  const sameUserNameOrEmail = (user = {}) => {
    if (searchText) {
      const {name, email} = user;
      const text = searchText.toLowerCase();
      return name.toLowerCase().includes(text) || email.toLowerCase().includes(text);
    } else {
      return true;
    }
  }

  const handleClearSearch = () => {
    setSearchText("");
  }

  const interactionUserName = (user) => {
    const {
      name,
      email,
    } = user;
    return {
      name,
      email,
    }
  }

  const getQuestionAnswer = (question, user) => {
    if (user && user.interaction_objects) {
      const interaction = user.interaction_objects.find(interaction => interaction.target_uid === question.id);
      if (interaction) {
        return interaction.answer || "";
      }
    }
    return "";
  }

  return (<S.ModalDiv
    role="dialog"
    aria-modal="true"
    aria-labelledby={labels.review || "Revisar"}
    aria-describedby={labels.review || "Revisar"}
    onKeyUp={handleEscape}
    hidden={!open}
  >
    <S.Form onSubmit={handleSubmit}>
      <S.Overlay onClick={handleClose}/>

      <S.ContentDiv>
        <S.ReviewContent>
          <S.MenuIcon
            tabIndex="1"
            type="button"
            onClick={handleStudentsMenu}
            primaryColor={primaryColor}
          >
            {
              isListOfItemsOpened
                ? iconExpandRight()
                : iconExpandLeft()
            }
          </S.MenuIcon>

          <S.CloseButton
            ref={closeRef}
            className={isListOfItemsOpened ? "visible" : ""}
            tabIndex="1"
            type="button"
            onClick={handleClose}
            primaryColor={primaryColor}
            title={labels.close || "Fechar"}
          >
            {iconClose()}
          </S.CloseButton>

          <S.Grid className={isListOfItemsOpened ? "visible" : ""}>
            <S.HeadRow className={isListOfItemsOpened ? "visible" : ""}>
              <S.SearchColumn>
                <S.SearchLabel
                  aria-label={labels.search || "Buscar"}
                >
                  <S.SearchInput
                    type="text"
                    name="search"
                    placeholder={labels.search || "Buscar"}
                    primaryColor={primaryColor}
                    value={searchText}
                    onChange={handleSearch}
                  />

                  {
                    searchText.length
                      ? (
                        <S.SearchInputIcon
                          role="button"
                          src={searchClearIcon}
                          onClick={handleClearSearch}
                        />
                      ) : (
                        <S.SearchInputIcon
                          role="button"
                          src={searchIcon}
                        />
                      )
                  }

                </S.SearchLabel>
              </S.SearchColumn>

              <S.AnsweredColumn
                type="button"
                background={primaryColor}
                onClick={handleSortByAnswered}
              >
                <p
                  style={{color: primaryColor, textDecoration: getStyleForOrder(sortAnsweredOrder)}}
                >
                  {labels.answered || "Respondido"}
                </p>
              </S.AnsweredColumn>

              <S.ReviewedColumn
                type="button"
                background={primaryColor}
                onClick={handleSortByReviewed}
              >
                <p
                  style={{color: primaryColor, textDecoration: getStyleForOrder(sortReviewedOrder)}}>
                  {labels.reviewed || "Corrigido"}
                </p>
              </S.ReviewedColumn>
            </S.HeadRow>

            <S.ReviewColumn>
              <S.Title primaryColor={primaryColor}>
                {interactionUserName(selectedUser).name}
              </S.Title>
            </S.ReviewColumn>
          </S.Grid>

          <S.Grid className={isListOfItemsOpened ? "visible" : ""}>
            <S.StudentList>
              {
                students
                  .filter(sameUserNameOrEmail)
                  .map((user, index) => (
                    <S.Student key={index}>
                      <S.StudentButton
                        tabIndex="1"
                        type="button"
                        onClick={() => handleStudentSelect(user)}
                        aria-selected={user.uid === selectedUser.uid}
                        primaryColor={primaryColor}
                      >
                        <S.BodyRow className={isListOfItemsOpened ? "visible" : ""}>
                          <S.StudentData>
                            <p>{user.name}</p>
                            <p>{user.email}</p>
                          </S.StudentData>

                          <S.AnsweredData>
                            {
                              (user.interaction_objects.find(objects => objects.answered === true)) && (
                                <S.CheckboxLabel
                                  aria-label={labels.answered_by_student || "Respondido pelo aluno"}
                                  checkColor={primaryColor}
                                >
                                  <S.CheckboxInput
                                    type="checkbox"
                                    readOnly
                                    checked
                                  />
                                  <S.Checkmark
                                    className="checkmark"
                                  />
                                </S.CheckboxLabel>
                              )
                            }
                          </S.AnsweredData>

                          <S.ReviewedData>
                            {
                              (user.count_reviewed > 0 && user.count_reviewed === questions.length) ? (
                                <S.CheckboxLabel
                                  aria-label={labels.reviewed || "Corrigido"}
                                  checkColor={primaryColor}
                                >
                                  <S.CheckboxInput
                                    type="checkbox"
                                    readOnly
                                    checked
                                  />
                                  <S.Checkmark className="checkmark"/>
                                </S.CheckboxLabel>
                              ) : (
                                <p>{user.count_reviewed}/{questions.length}</p>
                              )
                            }
                          </S.ReviewedData>
                        </S.BodyRow>
                      </S.StudentButton>
                    </S.Student>
                  ))
              }
            </S.StudentList>

            <S.ReviewData>
              <S.ReviewDataOverlay
                isVisible={Object.keys(selectedUser).length === 0}
              >
                <p style={{color: primaryColor}}>
                  {labels.select_to_start_review || "Selecione um aluno para efetuar a revisão."}
                </p>
              </S.ReviewDataOverlay>
              <S.QuestionList ref={questionListRef}>
                {questions.map((question, i) => (
                  <S.Question key={i}>
                    <S.Title primaryColor={primaryColor}>
                      {labels.question || "Questão"} {i + 1}
                    </S.Title>

                    <S.QuestionText
                      dangerouslySetInnerHTML={{__html: question.question || ""}}
                    />

                    <S.AnswerContainer>
                      <S.NoteContainer>
                        <NoteDropdown
                          notes={noteRange}
                          selected={(question.score !== null && question.score !== "" && question.score !== "null") ? parseInt(question.score) : null}
                          label={labels.score}
                          primaryColor={primaryColor}
                          onSelect={(note) => handleSelectNote(question, note)}
                          isLoadingNote={isLoadingNote.includes(question.id)}
                        />
                      </S.NoteContainer>

                      <S.AnswerLabel aria-label={labels.answer || "Resposta"}>
                        <S.Title primaryColor={primaryColor}>Resposta</S.Title>

                        <S.AnswerText
                          tabIndex="-1"
                          primaryColor={primaryColor}
                          name="answer"
                          rows="10"
                          readOnly
                          value={getQuestionAnswer(question, selectedUser)}
                        />
                      </S.AnswerLabel>
                    </S.AnswerContainer>

                  </S.Question>
                ))}
              </S.QuestionList>
            </S.ReviewData>
          </S.Grid>
        </S.ReviewContent>
      </S.ContentDiv>
    </S.Form>
  </S.ModalDiv>)
}

export default ReviewModal;