import React, {useEffect, useMemo, useState} from 'react';
import NoteDropdown from "./note-dropdown";
import {
  ActionButton,
  ActionItem,
  Actions,
  Author,
  Message,
  MessageContentItem,
  MessageHeader
} from "./forum-message.styled";
import PostLoading from "./post-loading";
import {isAdminReviewer, isContentOwner} from "../../utils/permission";
import API from "../../services";
import Quill from "./quill";

function ForumMessage(props) {
  const {
    id,
    user,
    rawContent,
    primaryColor = '#00643e',
    authorUID,
    column = 0,
    topicBackgroundColor,
    topicAuthorColor,
    topicTextColor,
    children,
    value,
    createdAtDateTime,
    createdAt,
    byLabel = "Por",
    customOptions,
    content,
    interactionObjects,
    language = "pt-br",
    onClickDelete,
    onClickReply,
    onClickEdit,
    onClickSaveNote = () => {},
    disableOperations = false,
    withReviewDropdown = false,
    elevation = 0,
  } = props;
  const [isVisible, setIsVisible] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [authorName, setAuthorName] = useState(null);
  const [notesRange, setNotesRange] = useState([]);
  const [note, setNote] = useState(null);
  const [text, setText] = useState(value);
  const [isLoadingNote, setIsLoadingNote] = useState(false);

  useEffect(() => {
    const getNoteRange = () => {
      const {
        grade_type,
        grade_numeral,
      } = content;

      switch (grade_type) {
        case 'numeral':
        default:
          const {min = 0, max = 10, steps = 1} = grade_numeral;
          let values = [];

          for (let i = min; i <= max; i += steps) {
            values.push(i);
          }

          return values;
      }
    }

    setNotesRange(getNoteRange)
  }, [content])

  useEffect(() => {
    if (Array.isArray(interactionObjects)) {
      const object = interactionObjects[0];
      if (object && Array.isArray(object.interaction_objects)) {
        const interaction = object.interaction_objects[0];

        if (interaction && interaction.score !== null) {
          setNote(parseInt(interaction.score));
        }
      }
    }
  }, [interactionObjects])

  useMemo(async () => {
    if (authorUID && authorName === null && isVisible) {
      const {
        access_token, api_env, domain, project_key,
      } = customOptions;

      try {
        const body = await API.auth.user.uid(authorUID, {
          access_token,
          api_env,
          domain,
          project_key,
        });
        setAuthorName(body.detail.name);
      } catch (e) {
        setAuthorName("");
      }
    }
  }, [customOptions, authorUID, authorName, isVisible])

  const getRemoveLabel = () => {
    const label = {
      "en": "Remove", "pt-br": "Excluir"
    }
    return label[language] || "Excluir";
  }

  const getReplyLabel = () => {
    const label = {
      "en": "Reply", "pt-br": "Comentar"
    }
    return label[language] || "Comentar";
  }

  const getEditLabel = () => {
    const label = {
      "en": "Edit", "pt-br": "Editar"
    }
    return label[language] || "Editar";
  }

  const getCancelLabel = () => {
    const label = {
      "en": "Cancel", "pt-br": "Cancelar"
    }
    return label[language] || "Cancelar";
  }

  const getSaveLabel = () => {
    const label = {
      "en": "Save", "pt-br": "Salvar"
    }
    return label[language] || "Salvar";
  }

  const handleSaveNote = (savedNote) => {
    if (Array.isArray(interactionObjects)) {
      const object = interactionObjects[0];
      if (object && Array.isArray(object.interaction_objects)) {
        const interaction = object.interaction_objects[0];

        if (interaction) {
          const prevNote = note;
          setNote(savedNote);

          setIsLoadingNote(true);

          API.cms
            .learning
            .interaction.uid(object.interaction_uid)
            .score
            .object.uid(interaction.uid)
            .updateScore(savedNote, customOptions)
            .then((resp) => {
              setIsLoadingNote(false);
              onClickSaveNote(savedNote, resp);
            })
            .catch(() => {
              setNote(prevNote);
              setIsLoadingNote(false);
            });
        } else {
          setIsLoadingNote(true);

          API.cms
            .learning
            .interaction.uid(object.interaction_uid)
            .object
            .reviewed({
              context_uid: customOptions.activity_uid,
              type: content.type,
              target_uid: rawContent.uid,
              object_data: rawContent,
              score: savedNote,
            }, customOptions)
            .then((resp) => {
              setNote(savedNote);
              setIsLoadingNote(false);
              onClickSaveNote(savedNote, resp);
            });
        }
      }
    }
  }

  const handleClickEdit = () => {
    setIsEditing(true);
  }

  const handleClickSave = () => {
    setIsEditing(false);
    onClickEdit(text);
  }

  const renderNoteDropdown = () => {
    return (
      <NoteDropdown
        notes={notesRange}
        primaryColor={primaryColor}
        onSelect={handleSaveNote}
        selected={note}
        isLoadingNote={isLoadingNote}
      />
    )
  }

  const renderOnlyNote = () => {
    return (
      <NoteDropdown
        notes={notesRange}
        primaryColor={primaryColor}
        selected={note}
        show={false}
      />
    )
  }

  if (authorName === null)
    return <PostLoading column={column} onScreen={() => setIsVisible(true)}/>

  return (
    <MessageContentItem
      backgroundColor={topicBackgroundColor}
      column={column}
      elevation={elevation}
    >
      <MessageHeader>
        <Author
          color={topicAuthorColor}
        >
          {`${byLabel}: ${authorName || ""} - `}
          <time dateTime={createdAtDateTime}>{createdAt}</time>
        </Author>

        {(withReviewDropdown && isAdminReviewer(user.roles) && (user.uid !== authorUID)) &&
          renderNoteDropdown()
        }

        {(!withReviewDropdown && note !== null) &&
          renderOnlyNote()
        }
      </MessageHeader>

      {
        (value && !isEditing) && (
          <Message
            color={topicTextColor}
            dangerouslySetInnerHTML={{__html: value}}
          />
        )
      }

      {
        (isEditing) && (
          <div>
            <Quill
              value={text}
              onChange={setText}
              placeholder="Escreva aqui..."
            />
          </div>
        )
      }

      {children}

      <Actions>
        <ActionItem>
          {
            isEditing ? (
              <>
                <ActionButton
                  type="button"
                  color={primaryColor}
                  onClick={handleClickSave}
                >
                  {getSaveLabel()}
                </ActionButton>

                <ActionButton
                  type="button"
                  color={primaryColor}
                  onClick={() => setIsEditing(false)}
                >
                  {getCancelLabel()}
                </ActionButton>
              </>
            ) : (
              !disableOperations && <>
                {onClickReply && (
                  <ActionButton
                    type="button"
                    color={primaryColor}
                    onClick={() => onClickReply(id)}
                  >
                    {getReplyLabel()}
                  </ActionButton>
                )}

                {(authorName && isContentOwner(user.uid || "", authorUID) && onClickEdit && note === null) && (
                  <ActionButton
                    type="button"
                    color={primaryColor}
                    onClick={handleClickEdit}
                  >
                    {getEditLabel()}
                  </ActionButton>
                )}

                {(authorName && isContentOwner(user.uid || "", authorUID) && onClickDelete && note === null) && (
                  <ActionButton
                    type="button"
                    color={primaryColor}
                    onClick={() => onClickDelete(id)}
                  >
                    {getRemoveLabel()}
                  </ActionButton>
                )}
              </>
            )
          }
        </ActionItem>
      </Actions>
    </MessageContentItem>
  )
}

export default ForumMessage;