import React from 'react';
import API from "../services";

export default function withObjectInteractionMonitor(WrappedComponent) {
  return function HOC(props) {
    const [requiredObjects, setRequiredObjects] = React.useState([]);
    const [events, setEvents] = React.useState([]);
    const [completedObjects, setCompletedObjects] = React.useState([]);
    const [completed, setCompleted] = React.useState(false);
    const [interactionActionsCount, setInteractionActionsCount] = React.useState([]);

    React.useEffect(() => {
      checkIfHasCompletedAllObjects();
    }, [completedObjects, requiredObjects, completed]);

    const onObjectInteractionEvent = (event) => {
      let newEvents = [...events, event];
      setEvents(newEvents);
      checkIfHasCompletedObject(event, newEvents);
    }

    const checkIfHasCompletedObject = (event, events) => {
      const { metadata, action } = event;
      const { element_id } = metadata;

      const object = requiredObjects.find(object => object.id === element_id);

      const completedObject = completedObjects.find(object => object.id === element_id);

      if (object && completedObject) {
        return;
      }

      let interactions = 0;

      if (object) {
        switch (object.type) {
          case "flashcards-1":
          case "flashcards-2":
          case "steps-1":
          case "accordion":
          case "quote-6":
          case "list-2":
          case "tabs":
          case "gallery-1":
            {
              let viewed = events.filter(event => event.metadata.element_id === element_id && event.action === "view");
              let clicked = events.filter(event => event.metadata.element_id === element_id && event.action === "click");
              let itensClicked = object.items.filter(item => clicked.find(event => event.metadata.item_id === item.id)) || [];
              let total = object.items.length;

              if (object.type === "tabs" || object.type === "quote-6" || object.type === "gallery-1" || object.type === "steps-1") {
                itensClicked.push(object.items[0])// add first tab as clicked
                total = total - 1;
                interactions -= 1;
              }

              let clickedAllCards = object.items.filter(item => clicked.find(event => event.metadata.item_id === item.id) !== undefined).length === total;

              if (itensClicked.length > 0) {
                interactions += itensClicked.length;
              }

              let interaction = {
                element_id,
                interactions,
                total
              };

              if (viewed && clickedAllCards) {
                addCompletedObject(object);
                interaction.finished = true;
              }

              setInteractionActionsCount([...interactionActionsCount.filter(i => i.element_id !== element_id), interaction]);
            }
            break;
          case "sorted-activity-1":
          case "audio-1":
          case "video-1":
            let ended = events.filter(event => event.metadata.element_id === element_id && event.action === "click" && event.verb === "ended");

            if (ended.length > 0) {
              addCompletedObject(object);
            }
            break;
          case "quiz-1":
          case "quiz-2":
          case "quiz-3":
          case "quiz-4":
          case "quiz-5":
          case "quiz-6":
          case "quiz-only-choice-result":
            {
              let quizResultTypes = [
                "quiz-only-choice-result",
                "quiz-multiple-choice-result",
                "quiz-fill-the-blankfeedback",
                "quiz-match-feedback",
                "quiz-essay-result",
                "quiz-mixed-result"
              ];
              let finished = events.filter(event => event.metadata.element_id === element_id && event.action === "view" && quizResultTypes.includes(event.element));

              if (finished.length > 0) {
                addCompletedObject(object);
              }
            }
            break;
          default:
            if (action === "view") {
              addCompletedObject(object);
            }
            break;
        }
      }
    }

    const sendMessageToPlayer = (action, message) => {
      try {
        const { activity_uid } = window.cstmptns || {};
        if (window.parent) window.parent.postMessage({
          type:"app_player",
          action: action,
          value: message,
          activity_uid,
        }, "*");
      } catch (error) {
        console.log("Error sending message to player", error);
      }
    }

    const addCompletedObject = async (object) => {
      setCompletedObjects([...completedObjects.filter(obj => obj.id !== object.id), object]);

      try {
        const { interaction_uid, activity_uid } = window.cstmptns || {};

        if (interaction_uid) {
          const objectEvents = events.filter(event => event.metadata.element_id === object.id);
          const interactions = interactionActionsCount.find(i => i.element_id === object.id);
          API.cms.learning.interaction
            .uid(interaction_uid)
            .createObject({
              context_uid: activity_uid,
              type: object.type,
              target_uid: object.id,
              object_data: {
                events: objectEvents,
                interactions,
                completed: true
              },
              score: "100",
            }, window.cstmptns);
        }
      } catch (error) {
        console.log("Error sending object interaction", error);

      }
    }

    const checkIfHasCompletedAllObjects = () => {
      if (!completed && requiredObjects.length > 0 && requiredObjects.length === completedObjects.length) {
        setCompleted(true);
        sendMessageToPlayer("interaction.progress", {
          required_objects: requiredObjects.length,
          completed_objects: completedObjects.length,
          completed: true
        })
      } else {
        sendMessageToPlayer("interaction.progress", {
          required_objects: requiredObjects.length,
          completed_objects: completedObjects.length,
          completed: false
        })
      }
    }

    return (
      <React.Fragment>
        <WrappedComponent
          {...props}
          _objectInteractionMonitor={{
            onObjectInteractionEvent,
            setRequiredObjects,
            requiredObjects,
            completed,
            completedObjects,
            setCompletedObjects,
            interactionActionsCount,
            setCompleted
          }}
        />
      </React.Fragment>
    );
  };
}