import React, {createRef} from "react";
import * as S from "./interaction-monitor-wrapper.styled";
import useOnScreen from "../../hooks/use-on-screen";

export default function InteractionMonitorObjectWrapper(props) {
  const {
    children,
    content,
    objectInteractionMonitor,
  } = props;
  const ref = createRef();
  const isOnScreen = useOnScreen(ref);
  const [isRequiredInteraction, setIsRequiredInteraction] = React.useState(false);
  const [completed, setCompleted] = React.useState(false);
  const [interaction, setInteraction] = React.useState(null);

  React.useEffect(() => {
    elementView();
  }, [isOnScreen]);

  React.useEffect(() => {
    if (!isRequiredInteraction && content && "required_interaction" in content && content.required_interaction) {
      setIsRequiredInteraction(true);
    }
  }, [content]);

  React.useEffect(() => {
    if (isRequiredInteraction && objectInteractionMonitor) {
      objectInteractionMonitor.completedObjects.find(object => object.id === content.id) !== undefined && setCompleted(true);
      let int = objectInteractionMonitor.interactionActionsCount.find(int => int.element_id === content.id);
      if (int && interaction && int.interactions > interaction.interactions) {
        setInteraction(int);
      } else if (int && interaction === null) {
        setInteraction(int);
      }
    }
  }, [isRequiredInteraction, objectInteractionMonitor.completedObjects, objectInteractionMonitor.interactionActionsCount]);

  const elementView = () => {
    if (isOnScreen) {
      const obj = {
        "action": "view",
        "element": content.type,
        "timestamp": Math.floor(Date.now() / 1000),
        "metadata": {
          "element_id": content.id,
          "element_title": content.content_title,
        }
      };
      if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
        obj.assets = content.dam_assets;
      }
      objectInteractionMonitor.onObjectInteractionEvent(obj);
    }
  }

  const renderProgressAlert = () => {
    return (
      <svg width="24" height="24" viewBox="0 0 24 24">
        <path fill="currentColor" d="M13,2V4C17.39,4.54 20.5,8.53 19.96,12.92C19.5,16.56 16.64,19.43 13,19.88V21.88C18.5,21.28 22.45,16.34 21.85,10.85C21.33,6.19 17.66,2.5 13,2M11,2C9.04,2.18 7.19,2.95 5.67,4.2L7.1,5.74C8.22,4.84 9.57,4.26 11,4.06V2.06M4.26,5.67C3,7.19 2.24,9.04 2.05,11H4.05C4.24,9.58 4.8,8.23 5.69,7.1L4.26,5.67M2.06,13C2.26,14.96 3.03,16.81 4.27,18.33L5.69,16.9C4.81,15.77 4.24,14.42 4.06,13H2.06M7.06,18.37L5.67,19.74C7.18,21 9.04,21.79 11,22V20C9.58,19.82 8.23,19.25 7.1,18.37H7.06M13,13V7H11V13H13M13,17V15H11V17H13Z" />
      </svg>
    )
  }

  const renderProgressCheck = () => {
    return (
      <svg width="24" height="24" viewBox="0 0 24 24">
        <path fill="currentColor" d="M13,2.03V2.05L13,4.05C17.39,4.59 20.5,8.58 19.96,12.97C19.5,16.61 16.64,19.5 13,19.93V21.93C18.5,21.38 22.5,16.5 21.95,11C21.5,6.25 17.73,2.5 13,2.03M11,2.06C9.05,2.25 7.19,3 5.67,4.26L7.1,5.74C8.22,4.84 9.57,4.26 11,4.06V2.06M4.26,5.67C3,7.19 2.25,9.04 2.05,11H4.05C4.24,9.58 4.8,8.23 5.69,7.1L4.26,5.67M15.5,8.5L10.62,13.38L8.5,11.26L7.44,12.32L10.62,15.5L16.56,9.56L15.5,8.5M2.06,13C2.26,14.96 3.03,16.81 4.27,18.33L5.69,16.9C4.81,15.77 4.24,14.42 4.06,13H2.06M7.1,18.37L5.67,19.74C7.18,21 9.04,21.79 11,22V20C9.58,19.82 8.23,19.25 7.1,18.37Z" />
      </svg>
    )
  }

  return (
    <React.Fragment>
      <S.Container ref={ref}>
        {(content && "show_interaction_progress" in content && content.show_interaction_progress) && (
          <S.IconsList className="interaction-monitor-status-container" title="progress">
            {isRequiredInteraction && (
              <React.Fragment>
                {completed ? (
                  <>
                    {renderProgressCheck()}
                  </>
                ) : (
                  <>
                    <S.InteractionStatusContainer>
                      {(interaction !== null && !interaction.finished && interaction.interactions) ? (
                        <S.InteractionCounter>
                          {interaction.interactions}/{interaction.total}
                        </S.InteractionCounter>
                      ) : null}

                      {renderProgressAlert()}
                    </S.InteractionStatusContainer>
                  </>
                )}
              </React.Fragment>
            )}
          </S.IconsList>
        )}
        {children}
      </S.Container>
    </React.Fragment>
  );
}