/**
 * Converte o objeto em uma query ‘string’
 *
 * @example
 *
 * objetoToQueryString({page: 1, limit: 10})
 * // => 'page=1&limit=10'
 *
 * @param {object} obj Objeto que será convertido para ‘string’
 * @return {string} Retorna uma ‘string’ com os valores do objeto
 */
const objectToQueryString = (obj) => {
  return Object.keys(obj).map((key) => {
    const value = obj[key];
    return `${key}=${value}`;
  }).join('&');
};

export default objectToQueryString;