import React, {useEffect, useRef} from 'react';
import useOnScreen from "../../hooks/use-on-screen";

function PostLoading(props) {
  const {
    onScreen = () => {},
    column = 1,
  } = props;
  const ref = useRef();
  const isVisible = useOnScreen(ref);

  useEffect(() => {
    if (isVisible) {
      onScreen();
    }
  }, [isVisible, onScreen]);

	return (<div ref={ref} className="element-forum-1-preloader">
		<div className="element-forum-1-preloader-message-children" style={{paddingLeft: (column * 40) + "px"}}>
			<div className="element-forum-1-preloader-message-child">
				<div
					className="element-forum-1-preloader-message-author element-forum-1-preloader-linear-background"/>
				<div
					className="element-forum-1-preloader-message-message element-forum-1-preloader-linear-background"/>
				<div
					className="element-forum-1-preloader-message-message element-forum-1-preloader-linear-background"/>
				<div className="element-forum-1-preloader-message-actions">
					<div
						className="element-forum-1-preloader-message-actions-item element-forum-1-preloader-linear-background"/>
				</div>
			</div>
		</div>
	</div>);
}

export default PostLoading;