import React, { Component } from 'react';
import {VisibleElement} from '../../utils/visible-element';
import './element-iframe.css';

export default class ElementIframe extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();
    this.handleChange = this.handleChange.bind(this);

    this.state = {
      "visible": false,
      "width": this.props.content.width,
      "height": this.props.content.height
    }
  }

  componentDidMount() {
    this.resizeWebview();
    this.onResizeIframe();
  }

  componentDidUpdate() {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "webview",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title,
            "url": content.url
          }
        }
        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  handleChange(content) {
    this.props.onChange(content);
  }

  onResizeIframe() {
    var _this = this;
    window.addEventListener("resize", function() {
      _this.resizeWebview()
    });
  }

  resizeWebview() {
    if (window.innerWidth < 801) {
      var current_width = this.state.width;
      var scale = (window.innerWidth / current_width * 1);
      this.setState({
        "width": window.innerWidth,
        "height": this.state.height * scale
      })
    }
  }
 
  render() {
    var content = this.props.content;
    var iframeContainerStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }

    var iframeStyle = {
      width: this.state.width + "px",
      height: this.state.height + "px"
    }

    return (<div id={content.id} ref={this.elementRef} className={this.props.className} style={iframeContainerStyle}>
      <iframe style={iframeStyle} src={content.url} allow="fullscreen" />
    </div>)
  }
}
