import objectToQueryString from "../utils/object-to-query-string";
import API from "./index";
import {isAdminReviewer} from "../utils/permission";

const CMS_API_VERSION = "v1.0";
const PROTOCOL = "https";
const SERVICE = "cms";

export default {
  learning: {
    register: {
      uid: (registerUid) => ({
        getInteractions: ({domain, api_env, project_key, access_token}) => {
          const base_url = `${PROTOCOL}://${domain}/${SERVICE}/${CMS_API_VERSION}`;
          return fetch(`${base_url}/learning/register/uid/${registerUid}/interactions`, {
            method: "GET",
            headers: {
              "Api-Env": api_env,
              "Project-Key": project_key,
              "Authorization": "Bearer " + access_token,
            },
          })
            .catch(e => {
              console.error(e);
              return e;
            })
        },
        interaction: {
          /**
           * @param {object=} query Objeto com os parâmetros da query
           * @param {UID=} query.context_uid Identificador da estrutura salva no composer
           * @param {UID=} query.target_uid Identificador do objeto de aprendizagem
           * @param {UID=} query.activity_uid Identificador do objeto de aprendizagem
           * @param {UID=} query.type Identificador do tipo do objeto de aprendizagem
           * @param {string} domain Hostname da API
           * @param {ApiEnvs} api_env Ambiente da API
           * @param {string} project_key Chave do projeto
           * @param {string} access_token Token JWT do usuário logado no momento
           * @return {Promise<Response>}
           */
          recoverObjects: ({type, ...query}, {domain, api_env, project_key, access_token}) => {
            const base_url = `${PROTOCOL}://${domain}/${SERVICE}/${CMS_API_VERSION}`;
            const queryString = query ? `?${objectToQueryString(query)}` : ``;
            return fetch(`${base_url}/learning/register/uid/${registerUid}/interaction/objects${queryString}`, {
              method: "GET",
              headers: {
                "Api-Env": api_env,
                "Project-Key": project_key,
                "Authorization": "Bearer " + access_token,
              },
            }).then(resp => resp.json())
              .then(objects => {
                if (objects && objects.length > 0) {
                  let parsed = objects.map(async object => {
                   try {
                     const userDetails = await API.auth.user.uid(object.user_uid, {
                       domain,
                       api_env,
                       project_key,
                       access_token
                     });

                     if (userDetails && userDetails.hasOwnProperty("error")) {
                       console.error(userDetails.error);
                       return null;
                     }

                     let roles = userDetails.roles.map(role => role.type.toLowerCase());

                     if (!isAdminReviewer(roles)) {
                       object.email = userDetails.login || "";
                       object.name = userDetails.detail.name;

                       object.interaction_objects = object.interaction_objects.map(interaction_object => {
                         interaction_object.object_data = JSON.parse(interaction_object.object_data);
                         return interaction_object;
                       });

                       if (type) {
                         object.interaction_objects = object.interaction_objects.filter(interaction_object => interaction_object.type === type);
                       }

                       return object;
                     } else {
                       return null;
                     }
                   } catch (e) {
                     console.error(e)
                     return null;
                   }
                  });
                  return Promise.all(parsed).then(parsed => parsed.filter(p => p !== null));
                }

                return [];
              })
          }
        }
      })
    },
    interaction: {
      /** @param {UID} interactionUid Identificador da interação */
      uid: (interactionUid) => ({
        object: {
          reviewed: ({context_uid, type, target_uid, object_data, score}, {
            domain,
            api_env,
            project_key,
            access_token
          }) => {
            const base_url = `${PROTOCOL}://${domain}/${SERVICE}/${CMS_API_VERSION}`;
            return fetch(`${base_url}/learning/interaction/uid/${interactionUid}/object/reviewed`, {
              method: "POST",
              headers: {
                "Api-Env": api_env,
                "Project-Key": project_key,
                "Authorization": "Bearer " + access_token,
                "Content-Type": "application/json"
              },
              body: JSON.stringify({
                context_uid,
                type,
                target_uid,
                object_data: JSON.stringify({
                  version: "1.0",
                  data: object_data
                }),
                score,
              })
            }).then(resp => {
              if (resp.status !== 200) {
                console.warn("Não foi possível salvar a resposta, verifique se o serviço está habilitado.")
              }
              return resp.json();
            }).catch(e => {
              console.error(e);
              return e;
            })
          }
        },
        score: {
          object: {
            uid: (interactionObjectUid) => ({
              updateScore: async (score, {domain, api_env, project_key, access_token}) => {
                const base_url = `${PROTOCOL}://${domain}/${SERVICE}/${CMS_API_VERSION}`;
                return fetch(`${base_url}/learning/interaction/uid/${interactionUid}/score/object/uid/${interactionObjectUid}?score=${score}`, {
                  method: "PUT",
                  headers: {
                    "Api-Env": api_env,
                    "Project-Key": project_key,
                    "Authorization": "Bearer " + access_token,
                  },
                }).then(resp => {
                  if (resp.status === 200) {
                    return resp.json();
                  }
                  throw resp;
                }).catch(e => {
                  console.error(e);
                  return e;
                })
                  .catch(e => {
                    console.error(e);
                    return e;
                  })
              }
            })
          }
        },
        /**
         * Salva uma interação dos objetos de aprendizagem no banco de dados
         * @param {UID} context_uid Identificador da estrutura salva no composer
         * @param {LearningObjectTypes} type Tipo do objeto de aprendizagem
         * @param {UID} target_uid Identificador do objeto de aprendizagem
         * @param {Quiz1Answer|Quiz2Answer[]|Quiz5Answer|Forum1Answer} object_data Dados do objeto de aprendizagem
         * @param {string=} score Pontuação do objeto de aprendizagem
         * @param {string} domain Hostname da API
         * @param {ApiEnvs} api_env Ambiente da API
         * @param {string} project_key Chave do projeto
         * @param {string} access_token Token JWT do usuário logado no momento
         * @return {Promise<Response>}
         */
        createObject: ({context_uid, type, target_uid, object_data, score}, {
          domain,
          api_env,
          project_key,
          access_token
        }) => {
          return fetch(`${PROTOCOL}://${domain}/${SERVICE}/${CMS_API_VERSION}/learning/interaction/uid/${interactionUid}/object`, {
            method: "POST",
            headers: {
              "Api-Env": api_env,
              "Project-Key": project_key,
              "Authorization": "Bearer " + access_token,
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              context_uid,
              type,
              target_uid,
              object_data: JSON.stringify({
                version: "1.0",
                data: object_data
              }),
              score,
            })
          }).then(resp => {
            if (resp.status !== 200) {
              console.warn("Não foi possível salvar a resposta, verifique se o serviço está habilitado.")
            }
            return resp
          }).catch(e => {
            console.error(e);
            return e;
          })
        },
        /**
         * @param {object=} query Objeto com os parâmetros da query
         * @param {UID=} query.context_uid Identificador da estrutura salva no composer
         * @param {UID=} query.target_uid Identificador do objeto de aprendizagem
         * @param {string} domain Hostname da API
         * @param {ApiEnvs} api_env Ambiente da API
         * @param {string} project_key Chave do projeto
         * @param {string} access_token Token JWT do usuário logado no momento
         * @return {Promise<Response>}
         */
        recoverObject: (query, {domain, api_env, project_key, access_token}) => {
          const base_url = `${PROTOCOL}://${domain}/${SERVICE}/${CMS_API_VERSION}`;
          const queryString = query ? `?${objectToQueryString(query)}` : ``;
          return fetch(`${base_url}/learning/interaction/uid/${interactionUid}/objects${queryString}`, {
            method: "GET",
            headers: {
              "Api-Env": api_env,
              "Project-Key": project_key,
              "Authorization": "Bearer " + access_token,
              "Content-Type": "application/json"
            },
          }).then(async resp => {
            const objects = await resp.json();

            if (objects && objects.length > 0) {
              return objects.map(object => {
                object.object_data = JSON.parse(object.object_data);

                return object;
              });
            }

            return [];
          }).catch(e => {
            console.error(e);
            return e;
          })
        }
      })
    }
  },
  socialmedia: {
    connector: {
      uid: (connectorUid) => ({
        createThread: (
          {title, description, content, context_type, context_uid},
          {domain, api_env, project_key, access_token}
        ) => {
          return fetch(`${PROTOCOL}://${domain}/cms/v1.1/socialmedia/connector/uid/${connectorUid}/thread`, {
            method: "POST",
            headers: {
              "Authorization": `Bearer ${access_token}`,
              "Project-Key": project_key,
              "Api-Env": api_env,
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              title,
              description,
              content,
              context_type,
              context_uid
            })
          }).then(resp => resp.json())
        }
      })
    },
    thread: {
      uid: (threadUid) => ({
        update: (
          {title, description, content, context_type, context_uid},
          {domain, api_env, project_key, access_token}
        ) => {
          return fetch(`${PROTOCOL}://${domain}/cms/v1.1/socialmedia/thread/uid/${threadUid}`, {
            method: "PUT",
            headers: {
              "Authorization": `Bearer ${access_token}`,
              "Project-Key": project_key,
              "Api-Env": api_env,
            },
            body: JSON.stringify({title, description, content, context_type, context_uid})
          }).then(resp => {
            if (resp.status === 500) {
              throw new Error(resp.statusText)
            } else {
              return resp.json();
            }
          });
        },
        delete: ({domain, api_env, project_key, access_token}) => {
          return fetch(`${PROTOCOL}://${domain}/cms/v1.1/socialmedia/thread/uid/${threadUid}`, {
            method: "DELETE",
            headers: {
              "Authorization": `Bearer ${access_token}`,
              "Project-Key": project_key,
              "Api-Env": api_env,
            }
          }).then(resp => {
            if (resp.status === 500) {
              throw new Error(resp.statusText)
            } else if (resp.status === 204) {
              return threadUid;
            }
            return null;
          });
        },
        post: {
          uid: (postUid) => ({
            delete: ({domain, api_env, project_key, access_token}) => {
              return fetch(`${PROTOCOL}://${domain}/cms/v1.1/socialmedia/post/uid/${postUid}`, {
                method: 'DELETE', headers: {
                  "Authorization": `Bearer ${access_token}`,
                  "Project-Key": project_key,
                  "Api-Env": api_env,
                }
              }).then(resp => {
                if (resp.status === 500) {
                  throw new Error(resp.statusText)
                } else if (resp.status === 204) {
                  return postUid;
                }
                return null;
              });
            },
            update: (
              {title, description, content, context_type, context_uid},
              {domain, api_env, project_key, access_token}
            ) => {
              return fetch(`${PROTOCOL}://${domain}/cms/v1.1/socialmedia/post/uid/${postUid}`, {
                method: "PUT",
                headers: {
                  "Authorization": `Bearer ${access_token}`,
                  "Project-Key": project_key,
                  "Api-Env": api_env,
                },
                body: JSON.stringify({title, description, content, context_type, context_uid})
              }).then(resp => {
                if (resp.status === 500) {
                  throw new Error(resp.statusText)
                } else {
                  return resp.json();
                }
              });
            },
          })
        },
        /**
         * @param {object} body
         * @param {string} body.title
         * @param {string} body.content
         * @param {string} domain Hostname da API
         * @param {ApiEnvs} api_env Ambiente da API
         * @param {string} project_key Chave do projeto
         * @param {string} access_token Token JWT do usuário logado no momento
         * @return {Promise<Response>}
         */
        createPost: (body, {domain, api_env, project_key, access_token}) => {
          return fetch(`https://${domain}/cms/v1.1/socialmedia/thread/uid/${threadUid}/post`, {
            method: 'POST',
            headers: {
              "Authorization": `Bearer ${access_token}`,
              "Project-Key": project_key,
              "Api-Env": api_env,
            },
            body: JSON.stringify(body)
          }).then(resp => resp.json())
        },
        /**
         * @param {object=} query Objeto com os parâmetros da query
         * @param {UID=} query.context_uid Identificador da estrutura salva no composer
         * @param {number=} query.page_size Tamanho da página
         * @param {number=} query.page Página a ser exibida
         * @param {string} domain Hostname da API
         * @param {ApiEnvs} api_env Ambiente da API
         * @param {string} project_key Chave do projeto
         * @param {string} access_token Token JWT do usuário logado no momento
         * @return {Promise<ResponsePaginated>}
         */
        recoverPosts: (query, {domain, api_env, project_key, access_token}) => {
          const queryString = query ? `?${objectToQueryString(query)}` : ``;
          return fetch(`https://${domain}/cms/v1.1/socialmedia/thread/uid/${threadUid}/posts${queryString}`, {
            method: 'GET', headers: {
              "Authorization": `Bearer ${access_token}`,
              "Project-Key": project_key,
              "Api-Env": api_env,
            }
          }).then(resp => resp.json());
        }
      }),
    },
    threads: {
      /**
       * @param {object=} query Objeto com os parâmetros da query
       * @param {UID=} query.context_uid Identificador da estrutura salva no composer
       * @param {number=} query.page_size Tamanho da página
       * @param {number=} query.page Página a ser exibida
       * @param {string} domain Hostname da API
       * @param {ApiEnvs} api_env Ambiente da API
       * @param {string} project_key Chave do projeto
       * @param {string} access_token Token JWT do usuário logado no momento
       * @return {Promise<ResponsePaginated>}
       */
      available: (
        query,
        {domain, api_env, project_key, access_token}
      ) => {
        const queryString = query ? `?${objectToQueryString(query)}` : ``;
        return fetch(`${PROTOCOL}://${domain}/cms/v1.1/socialmedia/threads/available${queryString}`, {
          method: "GET",
          headers: {
            "Authorization": `Bearer ${access_token}`,
            "Project-Key": project_key,
            "Api-Env": api_env,
          }
        }).then(resp => resp.json())
      }
    }
  }
}

/**
 * @typedef {object} Quiz1Answer
 * @property {string} id
 * @property {boolean} correct
 * @property {string} text Conteúdo HTML com o texto da alternativa
 */

/**
 * @typedef {object} Quiz2Answer
 * @property {string} id
 * @property {boolean} correct
 * @property {string} text Conteúdo HTML com o texto da alternativa
 */

/**
 * @typedef {string} Quiz5Answer Resposta do usuário
 */

/**
 * @typedef {string} Forum1Answer Post do usuário no fórum
 */

/**
 * @typedef {'quiz-1'|'quiz-2'|'quiz-5'|'forum-1'} LearningObjectTypes
 */

/**
 * @typedef {object} ResponsePaginated
 * @property {object} result
 * @property {number} total_pages
 */