import React, {useEffect, useRef, useState} from 'react';
import KeyCode from "../../utils/key-code";
import {ActionsDiv, Button, ContainerDiv, ContentDiv, Form, Overlay} from "./modal.styled";
import Quill from "./quill";
import Preloader from "./preloader";

function Modal(props) {
	const {
		open = false,
		onConfirm = (text) => text,
		onCancel = () => {},
		value = "",
		title = "Criar tópico",
		btnConfirmLabel = "Criar tópico",
		btnCancelLabel = "Cancelar",
    btnConfirmTextColor = "#fff",
    btnConfirmColor = "#000",
    btnCancelTextColor = "#fff",
    btnCancelColor = "#000",
    isLoading = false,
	} = props;
	const [text, setText] = useState(value);

  useEffect(() => {
    setText(value);
  }, [value]);

	const handleEscape = (event) => {
		let key = event.which || event.keyCode;

		if (key === KeyCode.ESC) {
			event.stopPropagation();
      onCancel();
		}
	}

	const handleSubmit = event => {
		event.preventDefault();
		onConfirm(text);
    setText("");
  }

	const handleClose = () => {
		onCancel();
    setText("");
  }

	return (<ContainerDiv
		role="dialog"
		aria-modal="true"
		aria-labelledby={title}
		onKeyUp={handleEscape}
		hidden={!open}
	>
		<Form onSubmit={handleSubmit}>
			<Overlay onClick={handleClose}/>

			<ContentDiv>
        <Quill
          value={text}
          onChange={setText}
          placeholder="Escreva aqui..."
        />

				<ActionsDiv>
					<Button
            type="submit"
            background={btnConfirmColor}
            color={btnConfirmTextColor}
            disabled={isLoading}
          >
						{btnConfirmLabel}
					</Button>

					<Button
						type="button"
						onClick={handleClose}
						style={{marginLeft: "15px"}}
            background={btnCancelColor}
            color={btnCancelTextColor}
            disabled={isLoading}
					>
						{btnCancelLabel}
					</Button>
				</ActionsDiv>
			</ContentDiv>
		</Form>
	</ContainerDiv>)
}

export default Modal;