import React, { Component } from 'react';
import './element-list.css';
import {VisibleElement} from "../../utils/visible-element";

export default class ElementList extends Component {
  constructor(props) {
    super(props);

    this.elementRef = React.createRef();

    this.state = {
      "content": this.props.content,
      "active_items": [],
      "visible": false
    }

    this.handleChange = this.handleChange.bind(this);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "list",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };

        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  trackElementClick(itemId) {
    var content = this.props.content;
    var obj = {
      "action": "click",
      "element": "list-2",
      "timestamp": Math.floor(Date.now() / 1000),
      "metadata": {
        "composition_uid": window.structure.metadata.content_uid,
        "element_id": content.id,
        "element_title": content.content_title,
        "item_id": itemId
      }
    };
    if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
      obj.assets = content.dam_assets;
    };

    this.props.onTrackEvent(obj);
  }

  handleChange(text, index) {
    var content = this.state.content;
    content.items[index].text = text;
    this.setState({
      "content": content
    });
    this.props.onChange(content);
  }

  renderQuill(item) {
    return <div className="editor" dangerouslySetInnerHTML={{__html: item.text}}></div>
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  onItemCheck(id) {
    if (!this.isItemActive(id)) {
      var items = this.state.active_items;
      items.push(id);
      this.setState({
        "items": items
      });
      this.trackElementClick(id);
    }
  }

  isItemActive(id) {
    var items = this.state.active_items;
    return items.filter(item => item === id).length > 0;
  }

  render() {
    var _this = this;
    let { headerActionsTemplate, footerActionsTemplate, content } = this.props;
    var listStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }
    var items = this.state.content.items;
    
    return <div ref={this.elementRef} className={`${this.props.className} ${this.getFont(content.font_family) || ""}`} style={listStyle}>
      {headerActionsTemplate}
      <ul className="element-list-items">
        {items.map(function(item, index) {
          return <li key={index} className="element-list-item">
            {content.type === "list-1" &&
              <span className="element-list-item-number" style={{
                backgroundColor: content.primary_color,
                color: content.secondary_color
              }}>{index + 1}</span>
            }
            {content.type === "list-2" &&
              <span className="element-list-item-checkbox"
                style={{
                  borderColor: content.primary_color,
                  color: content.secondary_color
                }}
                onClick={() => _this.onItemCheck(item.id)}
              >
                {_this.isItemActive(item.id) &&
                  <span className="element-list-item-checkbox-checked" style={{backgroundColor: content.primary_color}}>
                    <svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="768" height="768" viewBox="0 0 768 768">
                      <path fill={content.secondary_color} d="M617.376 169.376l-329.376 329.376-137.376-137.376c-12.512-12.512-32.768-12.512-45.248 0s-12.512 32.768 0 45.248l160 160c12.512 12.512 32.768 12.512 45.248 0l352-352c12.512-12.512 12.512-32.768 0-45.248s-32.768-12.512-45.248 0z"></path>
                    </svg>
                  </span>
                }
              </span>
            }
            {content.type === "list-3" &&
              <span className="element-list-item-bullet" style={{backgroundColor: content.primary_color}}></span>
            }
            <div className="element-list-item-text">
              {_this.renderQuill(item, index)}
            </div>
          </li>
        })}
      </ul>
      {footerActionsTemplate}
    </div>
  }
}
