import React, { Component } from 'react';
import {VisibleElement} from '../../utils/visible-element';
import './element-audio.css';

export default class ElementAudio extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.state = {
      "visible": false
    }

    this.onAudioPlay = this.onAudioPlay.bind(this);
    this.onAudioPause = this.onAudioPause.bind(this);
  }

  componentDidUpdate() {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "audio",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "url": content.audio,
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };
        
        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  trackEvent(verb, current, duration) {
    var content = this.props.content;
    var obj = {
      "action": "click",
      "element": "audio",
      "timestamp": Math.floor(Date.now() / 1000),
      "verb": verb,
      "metadata": {
        "url": content.audio,
        "composition_uid": window.structure.metadata.content_uid,
        "element_id": content.id,
        "element_title": content.content_title,
        "time": {
          "current": current,
          "duration": duration,
          "progress": current / duration * 100
        }
      }
    }
    this.props.onTrackEvent(obj);
  }

  onAudioPlay(e) {
    this.trackEvent("played", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  onAudioPause(e) {
    this.trackEvent("paused", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  onAudioEnded(e) {
    this.trackEvent("ended", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  onAudioSeeking(e) {
    this.trackEvent("skipped", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  render() {
    var content = this.props.content;
    var audioStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }

    return (<div id={content.id} ref={this.elementRef} className={this.props.className} style={audioStyle}>
      <audio
        controls src={content.audio}
        onPlay={(e) => this.onAudioPlay(e)}
        onPause={(e) => this.onAudioPause(e)}
        onEnded={(e) => this.onAudioEnded(e)}
        onSeeking={(e) => this.onAudioSeeking(e)}
      ></audio>
    </div>)
  }
}
