const USER_ROLES = {
  ADMIN: 'administratorglobal',
  REVIEWER: "reviewer",
  REVIEWER_ADMIN: "reviewadmin",
  REVIEWER_CONTRIBUTOR: "reviewcontributor",
  VIEWER: "viewer"
}
export const isContentOwner = (loggedUserUID, contentUserUID) => (loggedUserUID.length &&
  contentUserUID.length) && (loggedUserUID === contentUserUID)
export const isAdminReviewer = (roles = []) => roles.includes(USER_ROLES.REVIEWER_ADMIN) ||
  roles.includes(USER_ROLES.REVIEWER_CONTRIBUTOR) || roles.includes(USER_ROLES.ADMIN)
export const isAdminReviewerOrContentOwner = (user, contentUserUID) => isContentOwner(user.uid, contentUserUID) ||
  isAdminReviewer(user.roles || [])