export const ItemCard = styled.li`
  position: absolute;
  padding: 20px;
  left: calc(50% - 150px);
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  background-color: ${props => props.backgroundColor || "#FFFFFF"};
  border: 1px solid #ededed;
  color: ${props => props.fontColor || "#000"};
  width: 300px;
  height: 200px;
  border-top: 3px solid ${props => props.borderColor || "#00643e"};
  cursor: ${props => props.cursorStyle || "grab"};
  pointer-events: ${props => props.draggable !== "false" ? "all" : "none"}!important;
  box-shadow: 0 2px 5px rgb(0 0 0 / 10%);
  
  p {
    max-height: 130px;
    overflow: hidden;
  }

  &[draggable=true]:after {
    content: "";
    width: 24px;
    height: 24px;
    position: absolute;
    bottom: 12px;
    left: calc(50% - 12px);
    background: url("data:image/svg+xml;base64,");
  }
  
  ${props => props.isCorrect === false && css`
    opacity: 0;
    animation: shake 0.5s, disappear 0s;
    animation-delay: 0s, 0.5s;
  `}

  ${props => props.isCorrect === true && css`
    opacity: 0;
    transform: scale(0.8);
    animation: correct 1s, slide 0.5s;
    animation-delay: 0s, 1s;
    
    &::after {
      content: "";
      width: 34px;
      height: 34px;
      position: absolute;
      animation: grow 1s;
      top: -12px;
      left: -12px;
      background: url("data:image/svg+xml;base64,");
    }
  `}
  
  @keyframes grow {
    0% {
      width: 0;
      height: 0;
      top: 12px;
      left: 12px;
    }
    100% {
      width: 34px;
      height: 34px;
      top: -12px;
      left: -12px;
    }
  };

  @keyframes slide {
    0% {
      opacity: 1;
      top: 0;
    }
    100% {
      opacity: 0;
      top: 200px;
    }
  };
  
  @keyframes correct {
    0% {
      opacity: 1;
      transform: scale(1);
    }
    100% {
      opacity: 1;
      transform: scale(0.8);
    }
  };
  
  @keyframes shake {
    0% {
      opacity: 1;
      transform: translate(10px, 0);
    }
    25% {
      opacity: 1;
      transform: translate(-10px, 0);
    }
    50% {
      opacity: 1;
      transform: translate(10px, 0);
    }
    75% {
      opacity: 1;
      transform: translate(-10px, 0);
    }
    100% {
      opacity: 1;
      transform: translate(0, 0);
    }
  };

  @keyframes disappear {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  };
`;

