import React, {Component} from 'react';
import Moment from 'react-moment';
import './user-log.css';

export default class UserLog extends Component {
  constructor(props) {
    super(props);

    this.state = {
      "visible": false
    }
  }

  renderTimestamp(timestamp) {
    return <div className="rdp-composer-player-user-log-date">
      <Moment unix>{timestamp}</Moment>
    </div>
  }

  renderViewElement(event) {
    let _this = this;
    const {
      element,
    } = event;

    switch (element) {
      case "video":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o <b>player de vídeo</b> identificado como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "accordion":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o <b>accordion</b> identificado como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "audio":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o <b>player de áudio</b> identificado como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "header-1":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o <b>cabeçalho</b> identificado como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "webview":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a <b>webview</b> identificada como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "tabs":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o elemento de <b>tabs</b> identificado como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "flashcards":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o elemento de <b>flashcards</b> identificado
            como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "text":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou o <b>texto</b> identificado como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "image":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a <b>imagem</b> identificada como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "quiz-only-choice":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a <b>atividade de quiz de única escolha</b> identificada
            como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "quiz-only-choice-question":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a questão de título <b>{event.metadata.question.title}</b> da <b>atividade de
            quiz de única escolha</b> identificada como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "quiz-multiple-choice":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a <b>atividade de quiz de múltiplas escolhas</b> identificada
            como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "quiz-essay":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a <b>atividade de quiz dissertativo</b> identificada
            como <b>{event.metadata.element_title}</b>.
          </>
        )
      case "quiz-essay-question":
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário visualizou a questão de título <b>{event.metadata.question.title}</b> da <b>atividade de quiz
            dissertativo</b> identificada como <b>{event.metadata.element_title}</b>.
          </>
        )
      default:
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            {event.action}
          </>
        )
    }
  }

  renderClickElement(event) {
    let _this = this;
    const {
      element,
      verb,
    } = event;

    switch (element) {
      case "accordion":
        const verbLabel = verb === "opened" ? "abriu" : "fechou";
        return (
          <>
            {_this.renderTimestamp(event.timestamp)}
            O usuário {verbLabel} o item <b>{event.metadata.title}</b> do elemento <b>accordion</b> identificado
            como <b>{event.metadata.element_title}</b>.
          </>
        )
      default:
        return <>{event.action}</>
    }
  }

  render() {
    let _this = this;
    let events = this.props.events;

    return (
      <ul className="rdp-composer-player-user-log">
        {
          events.map(function (event, index) {
            let action = event.action;

            switch (action) {
              case "view":
                return (
                  <li key={index}>
                    {_this.renderViewElement(event)}
                  </li>
                )
              case "click":
                return (
                  <li key={index}>
                    {_this.renderClickElement(event)}
                  </li>
                )
              default:
                return <li key={index}>{event.action}</li>
            }
          })
        }
      </ul>
    )
  }
}
