  onApiEditPost(threadUid, postUid, text) {
    const {
      custom_options,
      content,
      user: {
        roles = []
      }
    } = this.state;
    const {
      activity_uid
    } = custom_options;

    return API.cms
      .socialmedia
      .thread.uid(threadUid)
      .post.uid(postUid)
      .update(
        {
          title: content.context_title || "title",
          content: text,
          context_type: "Activity",
          //TODO: change activity_uid to content.id
          context_uid: activity_uid
        },
        custom_options
      )
      .then(async resp => {
        if (!isAdminReviewer(roles)) {
          await this.createInteractionObject(resp);
        }

        const {
          forum_messages
        } = this.state;
        const {content} = resp;

        const thread = forum_messages.find(value => value.uid === threadUid);
        let post = thread.children.find(value => value.uid === postUid);
        post = {...post, content};
        thread.children = [...thread.children.filter(value => value.uid !== postUid), post];
        this.setState({forum_messages: [...forum_messages.filter(value => value.uid !== threadUid), thread]});
      });
  }

  onApiDeleteThread(uid) {
    const {
      custom_options,
      forum_messages
    } = this.state;

    return API.cms
      .socialmedia
      .thread
      .uid(uid)
      .delete(custom_options)
      .then((resp) => {
        if (resp !== null) {
          this.setState({
            forum_messages: forum_messages.filter(value => value.uid !== uid)
          })
        }
      });
  }

  handleConfirmAlertDialog() {
    const {alert_dialog_action, alert_dialog_action_data} = this.state;

    switch (alert_dialog_action) {
      case "delete_post":
        this.onApiDeletePost(alert_dialog_action_data)
          .then(this.resetAlertDialog)
          .catch(this.resetAlertDialog)
        break;
      case "delete_thread":
        this.onApiDeleteThread(alert_dialog_action_data)
          .then(this.resetAlertDialog)
          .catch(this.resetAlertDialog)
        break;
      default:
        this.setState({
          alert_dialog_action: null,
          alert_dialog_action_data: null,
          is_showing_alert_dialog: false,
        })
    }
  }

  handleDeletePost(uid, threadUid) {
    this.setState({
      is_showing_alert_dialog: true,
      alert_dialog_action: "delete_post",
      alert_dialog_action_data: {uid, threadUid}
    })
  }

  handleDeleteThread(uid) {
    this.setState({
      is_showing_alert_dialog: true,
      alert_dialog_action: "delete_thread",
      alert_dialog_action_data: uid
    })
  }

  resetAlertDialog(args) {
    this.setState({
      alert_dialog_action: null,
      alert_dialog_action_data: null,
      is_showing_alert_dialog: false,
    });
    return args
  }

  onApiDeletePost({uid, threadUid}) {
    const {
      custom_options,
    } = this.state;

    return API.cms
      .socialmedia
      .thread.uid(threadUid)
      .post.uid(uid)
      .delete(custom_options)
      .then(resp => {
        if (resp !== null) {
          const {
            forum_messages
          } = this.state;

          const forum_messages_new = forum_messages.map(thread => {
            if (thread.uid === threadUid) {
              thread.children = [...thread.children.filter(value => value.uid !== uid)];
            }
            return thread;
          });
          this.setState({forum_messages: forum_messages_new});
        }
      });
  }

  onApiGetThreadPosts(threadUid, page = 1) {
    let {
      custom_options,
      posts_page_size = 5
    } = this.state;

    return API.cms
      .socialmedia
      .thread.uid(threadUid)
      .recoverPosts(
        {
          page,
          page_size: posts_page_size,
        },
        custom_options
      );
  }

  async onApiSendPost(text) {
    const {
      custom_options,
      current_thread,
      content,
    } = this.state;

    try {
      const post = await API.cms
        .socialmedia
        .thread.uid(current_thread)
        .createPost(
          {
            title: content.title || "",
            content: text,
          },
          custom_options
        );

      await this.createInteractionObject(post);
      await this.appendPostIntoThread(post);
    } catch (e) {
      console.error(e);
      throw e;
    }
  }

  async createInteractionObject(object_data) {
    const {
      custom_options,
    } = this.state;
    const {
      content,
    } = this.props;
    const {
      interaction_uid,
      activity_uid,
    } = custom_options;

    return API.cms
      .learning
      .interaction.uid(interaction_uid)
      .createObject({
        //TODO: change activity_uid to content.id
        context_uid: activity_uid,
        type: content.type,
        target_uid: object_data.uid,
        object_data,
      }, custom_options)
  }

  appendPostIntoThread(post) {
    const {
      current_thread,
    } = this.state;
    console.log("appendPostIntoThread", post);
    this.setState(prevState => {
      const updatedThread = prevState.forum_messages.find(thread => thread.uid === current_thread)
      updatedThread.children = [...updatedThread.children, post];
      const filtered = prevState.forum_messages.map(thread => {
        if (thread.uid !== updatedThread.uid)
          return thread
        else
          return updatedThread
      })
      return {
        forum_messages: filtered
      }
    })
  }

  handleChange(content) {
    const {
      onChange = () => console.warn("No onChange defined")
    } = this.props;

    onChange(content);
  }

  getFormatDate(date = "") {
    let language = this.state.language;
    if (date.length) {
      let date_whitout_milisseconds = date.split(".")[0] + "Z";
      if (language === "en") {
        return moment(date_whitout_milisseconds).local().format("MM/DD/YYYY - HH:mm:ss");
      } else {
        return moment(date_whitout_milisseconds).local().format("DD/MM/YYYY - HH:mm:ss");
      }
    }
    return ""
  }

  getByLabel() {
    const {language} = this.state;
    const label = {
      "en": "By", "pt-br": "Por"
    }
    return label[language] || "Por";
  }

  sendThread(text) {
    const {
      forum_messages,
    } = this.state;

    const backup = [...forum_messages];

    this.setState({
      is_loading_new_topic_modal_confirmation: true,
      forum_messages: []
    })

    this.onApiSendThread(text)
      .then(() => {
        this.setState({
          is_showing_new_topic_modal: false,
          is_loading_new_topic_modal_confirmation: false
        })
      })
      .catch(() => {
        this.setState({
          is_loading_new_topic_modal_confirmation: false,
          forum_messages: backup,
        })
      })
  }

  sendPost(text) {
    this.setState({
      is_loading_new_reply_thread_modal_confirmation: true
    })
    this.onApiSendPost(text)
      .then(() => {
        this.setState({
          is_showing_reply_thread_modal: false,
          is_loading_new_reply_thread_modal_confirmation: false
        })
      })
      .catch(() => {
        this.setState({
          is_loading_new_reply_thread_modal_confirmation: false
        })
      })
  }

