import React, {Component} from 'react';
import {VisibleElement} from '../../utils/visible-element';
import {StringFormat} from '../../utils/string-format';
import './element-quiz-5.css';
import {
  ContainedButton,
  ContainerFinishButton,
  FeedbackContainer,
  NextButtonContainer,
  OutlinedButton,
  PrevButtonContainer,
  RemakeButtonContainer,
  RemakeButtonIcon,
  RemakeButtonText,
  TextArea,
  TextAreaContainer,
  TextButton
} from "./element-quiz-5.styled";
import API from "../../services";
import {isAdminReviewer} from "../../utils/permission";
import ReviewModal from "./review-modal";
import jwtDecode from "jwt-decode";

export default class ElementQuiz5 extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.loadContentFromServer = this.loadContentFromServer.bind(this);
    this.recoverObjects = this.recoverObjects.bind(this);
    this.updateQuestionsBasedOnObjects = this.updateQuestionsBasedOnObjects.bind(this);
    this.verifyIfHasAnweredAllQuestions = this.verifyIfHasAnweredAllQuestions.bind(this);
    this.showResume = this.showResume.bind(this);
    this.getSignedUserFromJwt = this.getSignedUserFromJwt.bind(this);
    this.renderStats = this.renderStats.bind(this);
    this.renderConfirmAnswerButton = this.renderConfirmAnswerButton.bind(this);
    this.renderEditAnswerButton = this.renderEditAnswerButton.bind(this);
    this.renderDissertationArea = this.renderDissertationArea.bind(this);
    this.renderConfirmAnswerButton = this.renderConfirmAnswerButton.bind(this);
    this.handleCloseReviewModal = this.handleCloseReviewModal.bind(this);
    this.getNoteRange = this.getNoteRange.bind(this);
    this.createInteractionObject = this.createInteractionObject.bind(this);

    this.state = {
      content: props.content,
      contentBkp: props.content,
      activeAnswers: {},
      currentQuestionPos: 0,
      current_question: null,
      answeredQuestionsQtd: 0,
      attempsMade: 0,
      answered_qtd: 0,
      questionAnswer: "",
      visible: false,
      custom_options: window.cstmptns || {},
      is_review_modal_open: false,
      is_loading_review_modal: false,
      user: {
        roles: []
      },
    };
  }

  componentDidMount() {
    this.getSignedUserFromJwt()
      .then(this.loadContentFromServer);
    this.sortQuestions();
  }

  componentDidUpdate(prevProps, prevStates) {
    this.trackElementView();
  }

  sortQuestions(isRemake = false) {
    const contentCopy = { ...this.props.content };
    var splitQuestions = contentCopy.number_of_questions_to_display !== undefined && contentCopy.number_of_questions_to_display > 0;
    var randomizeOnRemake = contentCopy.random_on_remake !== undefined && contentCopy.random_on_remake.enabled === true;
    var sortQuestions = contentCopy.random_question_order !== undefined && contentCopy.random_question_order.enabled === true;

    if ((randomizeOnRemake && isRemake) || !isRemake) {
      const backup = this.props.content.questions;
      var newQuestions = this.props.content.questions;

      if (sortQuestions) {
        newQuestions.sort((a, b) => {
          return Math.floor(Math.random() * (1 - -1)) + -1;
        });
      }

      if (splitQuestions) {
        newQuestions = newQuestions.slice(0, contentCopy.number_of_questions_to_display);
      }

      contentCopy.questions = newQuestions;

      this.props.content.questions = backup;
      this.setState({
        content: contentCopy
      })
    }

    return contentCopy;
  }

  loadContentFromServer() {
    this.recoverObjects()
      .then(this.updateQuestionsBasedOnObjects)
      .then(this.verifyIfHasAnweredAllQuestions);
  }

  async recoverObjects() {
    const {
      content,
    } = this.props;
    const {
      custom_options,
    } = this.state;
    const {
      interaction_uid,
    } = custom_options;

    return new Promise(async (resolve, reject) => {
      if (content) {
        const objects = await API.cms.learning.interaction
          .uid(interaction_uid)
          .recoverObject({
            context_uid: content.id,
          }, custom_options);

        if (objects && objects.length > 0) {
          resolve(objects);
        }
      }

      reject();
    })
  }

  async updateQuestionsBasedOnObjects(objects = []) {
    if (objects.length > 0) {
      const {
        content,
      } = this.state;
      let contentCopy = {...content};
      let attempsMade = 0;
      let current_question = 0;
      let answered_qtd = 0;

      contentCopy.questions = contentCopy.questions.map((question, index) => {
        let lastObjectDate = null;
        const objectTries = objects.filter((object) => {
          return object.target_uid === question.id;
        }).map(object => {

          if (lastObjectDate === null) {
            lastObjectDate = object.created_at;
          } else {
            if (object.created_at > lastObjectDate) {
              lastObjectDate = object.created_at;
            }
          }

          return object;
        });

        attempsMade = objectTries.length;

        const object = objectTries.find((obj) => {
          return obj.created_at >= lastObjectDate;
        });

        if (object) {
          current_question = index;
          answered_qtd++;
          question.answered = true;
          const answer = object.object_data;

          if (answer && answer.data) {
            this.updateActiveChoisesBasedOnAnswer(question, answer.data);
          }
        }

        return question;
      });

      this.setState({
        content: contentCopy,
        attempsMade,
        current_question,
        answered_qtd,
      });
    }

    return objects;
  }

  updateActiveChoisesBasedOnAnswer(question, answer) {
    let activeAnswers = {...this.state.activeAnswers};
    activeAnswers[question.id] = answer;

    this.setState({
      activeAnswers,
    });
  }

  verifyIfHasAnweredAllQuestions(objects = []) {
    const {
      answered_qtd,
      content: {
        questions,
      },
    } = this.state;

    if (answered_qtd === questions.length) {
      this.showResume();
    }

    return objects;
  }

  showResume() {
    const {
      content,
    } = this.state;
    const {questions} = content;

    let total = questions.length;

    const resume = {
      rdp_html_content: {
        questions: questions,
        total: total,
      },
    };

    this.setState({
      grades: resume,
      show_result: true,
      answered: true,
    });
  }

  trackElementView() {
    let content = this.props.content;
    let isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible) {
        let obj = {
          action: "view",
          element: "quiz-essay",
          timestamp: Math.floor(Date.now() / 1000),
          metadata: {
            composition_uid: window.structure.metadata.content_uid,
            element_id: content.id,
            element_title: content.content_title,
          },
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        }
        this.props.onTrackEvent(obj);
        this.trackQuestionView();
      }
      this.setState({
        visible: isVisible,
      });
    }
  }

  trackQuestionView() {
    let content = this.props.content;
    let currentQuestion = content.questions[this.state.currentQuestionPos];
    let obj = {
      action: "view",
      element: "quiz-essay-question",
      verb: "viewed",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: currentQuestion.id,
          position: this.state.currentQuestionPos + 1,
          title: StringFormat.HtmlToText(currentQuestion.question),
        },
      },
    };
    this.props.onTrackEvent(obj);
  }

  trackAnswerConfirmClick(question) {
    const { activeAnswers } = this.state;
    try {
      let content = this.props.content;
      if (question !== null) {
        let obj = {
          action: "click",
          element: "quiz-essay-question-confirm-answer",
          verb: "answered",
          timestamp: Math.floor(Date.now() / 1000),
          metadata: {
            composition_uid: window.structure.metadata.content_uid,
            element_id: content.id,
            element_title: content.content_title,
            question: {
              id: question.id,
              position: this.state.currentQuestionPos + 1,
              title: StringFormat.HtmlToText(question.question),
              answer: activeAnswers[question.id],
            },
          },
        };
        this.props.onTrackEvent(obj);
      }
    } catch (e) {
      console.error(e)
    }
  }

  trackNavigation(direction) {
    let content = this.props.content;
    let element =
      direction === "next"
        ? "quiz-essay-question-navigation-next"
        : "quiz-essay-question-navigation-previous";
    let source =
      direction === "next"
        ? this.state.currentQuestionPos - 1
        : this.state.currentQuestionPos + 1;
    let source_question = this.props.content.questions[source];
    let question = this.props.content.questions[this.state.currentQuestionPos];

    let obj = {
      action: "click",
      element: element,
      timestamp: Math.floor(Date.now() / 1000),
      verb: direction === "next" ? "advanced" : "retreated",
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: question.id,
          position: this.state.currentQuestionPos + 1,
          title: StringFormat.HtmlToText(question.question),
        },
        source_question: {
          id: source_question.id,
          position:
            (direction === "next"
              ? this.state.currentQuestionPos - 1
              : this.state.currentQuestionPos + 1) + 1,
          title: StringFormat.HtmlToText(source_question.question),
        },
      },
    };
    this.props.onTrackEvent(obj);
  }

  trackQuizResult(questions, resume) {
    let evaluative = false;

    for (let i = 0; i < questions.length; i++) {
      if (!questions[i].no_correct_answer) {
        evaluative = true;
        break;
      }
    }

    let content = this.props.content;
    let obj = {
      action: "view",
      verb: "viewed",
      element: "quiz-essay-result",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        questions: questions,
      },
    };

    if (evaluative === true) {
      obj.metadata.result = {
        hits: resume.rdp_html_content.hits,
        total: resume.rdp_html_content.total,
        percentage_average:
          (100 / resume.rdp_html_content.total) * resume.rdp_html_content.hits,
        min_percentage: resume.rdp_html_content.min_percentage,
      };
    }

    this.props.onTrackEvent(obj);
  }

  trackRemakeQuiz() {
    let content = this.props.content;
    let obj = {
      action: "click",
      element: "quiz-essay-remake",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
      },
    };
    this.props.onTrackEvent(obj);
  }

  async getSignedUserFromJwt() {
    const {custom_options: {access_token}} = this.state;

    return new Promise((resolve, reject) => {
      let user = jwtDecode(access_token);

      if (user) {
        let roles = [];
        if (user.role && Array.isArray(user.role)) {
          roles = user.role.map(role => role.toString().toLowerCase())
        } else if (user.role && typeof user.role === "string") {
          roles.push(user.role.toString().toLowerCase())
        }
        user = {...user, roles, uid: user.oid || ""};
        this.setState({
          user
        }, () => {
          resolve(user);
        });
      } else {
        reject("Error jwt decode");
      }
    });
  }

  getLabels() {
    const {
      content: {
        questions = []
      }
    } = this.state;
    let language = this.props.contentLanguage;
    if (language === undefined) language = "pt_br";
    if (language === "en") {
      return {
        confirm: "Confirm",
        edit: "Edit answer",
        question: "Question",
        back: "Back",
        retry: "Try again",
        previous: "Previous",
        next: "Next",
        finish: questions.length > 1 ? "Send answers" : "Send answer",
        incorrect: "Incorrect",
        right: "Right!",
        finish_quiz_message: "You have completed this quiz.",
        answer_saved: "Answer saved!",
        stats: "Statistics",
        review: "Review answers",
        answer: "Answer",
        reviewed: "Reviewed",
        answered_by_student: "Answered by student",
        answered: "Answered",
        search: "Search",
        finish_review: "Finish review",
        close: "Close",
        score: "Score",
        select_to_start_review: "Select a student to review."
      };
    } else if (language === "pt_br") {
      return {
        confirm: "Confirmar",
        edit: "Editar resposta",
        question: "Questão",
        retry: "Refazer atividade",
        previous: "Anterior",
        next: "Próximo",
        finish: questions.length > 1 ? "Enviar respostas" : "Enviar resposta",
        incorrect: "Incorreto",
        right: "Correto!",
        finish_quiz_message: "Você finalizou este questionário.",
        answer_saved: "Resposta salva!",
        stats: "Estatísticas",
        review: "Revisar respostas",
        answer: "Resposta",
        reviewed: "Revisado",
        answered_by_student: "Respondido pelo aluno",
        answered: "Respondido",
        search: "Pesquisar",
        finish_review: "Finalizar revisão",
        close: "Fechar",
        score: "Nota",
        select_to_start_review: "Selecione um aluno para efetuar a revisão."
      };
    }
  }

  renderQuestionQuill(question) {
    return (
      <div className="element-quiz-5-question">
        <div
          className="editor"
          dangerouslySetInnerHTML={{__html: question.question}}
        />
      </div>
    );
  }

  handleChangeAnswer(question, questionAnswer) {
    let contentClone = this.state.content;
    let activeAnswers = this.state.content;

    activeAnswers[question.id] = questionAnswer;

    this.setState({
      content: contentClone,
      questionAnswer,
      activeAnswers,
    });
  }

  renderDissertationArea(question) {
    const {
      content: {
        primary_color
      },
      activeAnswers,
    } = this.state;

    return (
      <TextAreaContainer>
        <TextArea
          tabIndex={1}
          value={activeAnswers[question.id] || ""}
          onChange={event => this.handleChangeAnswer(question, event.target.value)}
          disabled={question.answered}
          primaryColor={primary_color}
          rows={10}
        />
      </TextAreaContainer>
    )
  }

  renderPreviousQuestionBtn() {
    const {
      content: {
        primary_color
      },
      currentQuestionPos
    } = this.state;

    if (currentQuestionPos > 0) {
      return (
        <PrevButtonContainer>
          <TextButton
            tabIndex={1}
            type="button"
            color={primary_color}
            onClick={() => this.onNavigateQuestions('prev')}
          >
            {this.getLabels().previous}
          </TextButton>
        </PrevButtonContainer>
      )
    }
  }

  onNavigateQuestions(direction) {
    const current = this.state.currentQuestionPos;
    const currentQuestionPos = direction === "next" ? current + 1 : current - 1;

    this.setState(
      {
        currentQuestionPos
      },
      function () {
        this.trackNavigation(direction);
        this.trackQuestionView();
      }
    );
  }

  renderNextQuestionBtn() {
    const {
      currentQuestionPos,
      content: {
        questions,
        primary_color
      }
    } = this.state;
    const nextQuestionPos = currentQuestionPos + 1;

    if (nextQuestionPos < questions.length) {
      return (
        <NextButtonContainer>
          <TextButton
            tabIndex={1}
            type="button"
            color={primary_color}
            onClick={() => this.onNavigateQuestions('next')}
          >
            {this.getLabels().next}
          </TextButton>
        </NextButtonContainer>
      )
    }
  }

  renderFinishBtn() {
    const {currentQuestionPos, content, user} = this.state;
    const {questions, primary_color} = content;
    let current = currentQuestionPos + 1;
    let questions_count = questions.length;
    let answeredQuestions = 0;
    questions.map(q => {
      if (q.answered) {
        answeredQuestions++;
      }
    })
    let disabledBtn = questions_count !== answeredQuestions || isAdminReviewer(user.roles);
    if (current === questions_count) {
      return (
        <ContainerFinishButton
          color={!disabledBtn ? primary_color : "#A1A1A1"}
        >
          <ContainedButton
            tabIndex={1}
            type="button"
            background={!disabledBtn ? primary_color : "#A1A1A1"}
            disabled={disabledBtn}
            onClick={() => this.onFinishClick()}
          >
            {this.getLabels().finish}
          </ContainedButton>
        </ContainerFinishButton>
      )
    }
  }

  renderProgress() {
    const {currentQuestionPos, content} = this.state;
    const {questions, primary_color} = content;
    const current = currentQuestionPos + 1;
    const total = questions.length;
    const percentage = (100 / total) * current + "%";
    return (
      <div className="rdp-react-quiz-5-navigation-progress">
        <div className="rdp-react-quiz-5-navigation-progress-bar-placeholder">
          <div
            style={{width: percentage, backgroundColor: primary_color}}
            className="rdp-react-quiz-5-navigation-progress-bar"
          />
        </div>
        <span
          style={{color: primary_color}}
          className="rdp-react-quiz-5-navigation-progress-count"
        >
          {current}/{total}
        </span>
      </div>
    );
  }

  resetAllQuestions() {
    let cloneContent = this.state.content;
    cloneContent.questions = this.state.content.questions.map(question => {
      question.answered = false;
      question.answer = "";
      return question;
    });
    this.setState({
      content: cloneContent
    }, () => this.defineAnswered(0));
  }

  defineAnswered = (idQuestion, answered = true) => {
    let cloneContent = this.state.content;
    let questionObj = null;

    cloneContent.questions = this.state.content.questions.map(question => {
      if (question.id === idQuestion) {
        question.answered = answered;
        questionObj = question;
      }
      return question;
    });

    this.setState({
        content: cloneContent
      },
      () => {
        try {
          this.props.handleComplete(cloneContent, this.state.activeAnswers);
        } catch (e) {
          console.error(e)
        }
        this.trackAnswerConfirmClick(questionObj);
      }
    );
  }

  verifyAnswer = (question) => {
    const {feedback} = this.state.content;

    if (feedback.type !== "disabled" && !question.no_feedback) {
      return (
        <div className="element-quiz-5-feedback-msg">
          {this.getLabels().answer_saved}
        </div>
      );
    }
  };

  renderFeedback(question) {
    return (
      <div className="element-quiz-5-feedback-container">
        {this.verifyAnswer(question)}
        {this.renderEditAnswerButton(question)}
      </div>
    );
  }

  renderConfirmAnswerButton(question) {
    const {content, activeAnswers} = this.state;
    const {primary_color} = content;

    let currentAnswer = activeAnswers[question.id] || "";
    const enabled = currentAnswer.length > 0;

    return (
      <FeedbackContainer>
        <ContainedButton
          tabIndex={1}
          type="button"
          onClick={() => this.defineAnswered(question.id)}
          background={enabled ? primary_color : "#F1F1F1"}
          disabled={!enabled}
        >
          {this.getLabels().confirm}
        </ContainedButton>
      </FeedbackContainer>
    )
  }

  renderEditAnswerButton(question) {
    const {content} = this.state;
    const {primary_color} = content;

    return (
      <FeedbackContainer>
        <ContainedButton
          tabIndex={1}
          type="button"
          onClick={() => this.defineAnswered(question.id, false)}
          background={primary_color}
        >
          {this.getLabels().edit}
        </ContainedButton>
      </FeedbackContainer>
    )
  }

  renderStats(question) {
    const {
      content: {
        primary_color
      },
      is_loading_review_modal
    } = this.state;

    return (
      <div>
        <div className="rdp-react-quiz-5-stats-container">
          {/*<h3 className="rdp-react-quiz-5-stats-container-title">*/}
          {/*  {this.getLabels().stats}*/}
          {/*</h3>*/}
          <div className="rdp-react-quiz-5-stats-container-content">
            {/*<ul>*/}
            {/*  <li>usuários que finalizaram</li>*/}
            {/*  <li>usuários que não finalizaram</li>*/}
            {/*</ul>*/}

            <OutlinedButton
              tabIndex={1}
              type="button"
              onClick={() => {
                this.setState({
                  is_loading_review_modal: true,
                }, () => this.startReviewForQuestion(question))
              }}
              background={primary_color}
              disabled={is_loading_review_modal}
            >
              {this.getLabels().review}...
            </OutlinedButton>
          </div>
        </div>
      </div>
    )
  }


  startReviewForQuestion(question) {
    this.getUsersInteractions()
      .then(interaction_objects => {
      this.setState({
        is_review_modal_open: true,
        is_loading_review_modal: false,
        current_question: question,
        interaction_objects
      })
    });
  }

  handleCloseReviewModal() {
    this.setState({
      is_review_modal_open: false,
    })
  }

  renderQuestionare(question) {
    const {
      currentQuestionPos,
      content: {
        questions,
        primary_color
      },
      user: {
        roles = []
      }
    } = this.state;

    return (
      <div>
        <div className="element-quiz-5-header">
          <span style={{color: primary_color}}>
            {this.getLabels().question} {currentQuestionPos + 1}
          </span>
          <span style={{color: primary_color}}>
            {currentQuestionPos + 1}/{questions.length}
          </span>
        </div>
        {this.renderQuestionQuill(question)}
        {isAdminReviewer(roles) ? this.renderStats(question) : (
          <>
            {this.renderDissertationArea(question)}
            {
              question.answered
                ? this.renderFeedback(question)
                : this.renderConfirmAnswerButton(question)
            }
          </>
        )}
        <div className="rdp-react-quiz-5-navigation">
          {this.renderPreviousQuestionBtn()}
          {this.renderProgress()}
          {this.renderNextQuestionBtn()}
          {!isAdminReviewer(roles) && this.renderFinishBtn()}
        </div>
      </div>
    );
  }

  renderContent() {
    const {
      currentQuestionPos,
      content: {
        questions
      },
      show_result
    } = this.state;

    if (questions && questions.length) {
      let question = questions[currentQuestionPos];
      return (
        <div className="element-quiz-5-container">
          {question && !show_result
            ? this.renderQuestionare(question)
            : this.renderResult()}
        </div>
      );
    }
  }

  async createInteractionObject(question) {
    const {
      content,
      custom_options,
      activeAnswers,
    } = this.state;
    const {
      interaction_uid,
    } = custom_options;

    return API.cms.learning.interaction
      .uid(interaction_uid)
      .createObject({
        context_uid: content.id,
        type: content.type,
        target_uid: question.id,
        object_data: activeAnswers[question.id],
      }, custom_options);
  }

  onFinishClick() {
    const {
      content,
      activeAnswers,
      attempsMade,
    } = this.state;
    const {questions} = content;

    for (const question of questions) {
      this.createInteractionObject(question);
    }

    let total = questions.length;

    let resume = {
      rdp_html_content: {
        questions: questions,
        total: total,
      },
    };

    this.setState(
      {
        grades: resume,
        show_result: true,
        answered: true,
        attempsMade: attempsMade + 1,
      },
      () => {

        this.trackQuizResult(questions, resume);

        try {
          this.props.handleComplete(content, activeAnswers);
          this.props.handleFinish(content, {
            score: "0",
            finished: true,
          });
        } catch (e) {
          console.error(e);
        }
      }
    );
  }

  onRemakeQuestionnaire() {
    this.resetAllQuestions();
    this.setState(prevState => ({
      answered: false,
      grades: null,
      show_result: false,
      currentQuestionPos: 0,
      activeAnswers: [],
      questionAnswer: "",
      attempsMade: prevState.attempsMade + 1,
    }));

    this.sortQuestions(true);

    this.trackRemakeQuiz();
  }

  getResultLabel() {
    let language = this.props.contentLanguage;
    if (language === "en") {
      return "Your responses have been sent for review.";
    } else if (language === "pt_br") {
      return "Suas respostas foram enviadas para análise.";
    }
  }

  renderFinalMessage() {
    const feedback = this.state.content.feedback.type;

    if (feedback === "disabled") {
      return <span className="rdp-react-quiz-5-result-message">{this.getLabels().finish_quiz_message}</span>
    } else {
      return (
        <span className="rdp-react-quiz-5-result-message">
          {this.getResultLabel()}
        </span>
      )
    }
  }

  userCanRemakeQuiz = () => {
    const {content, attempsMade} = this.state;
    const {
      max_attempts = null,
    } = content;
    // É comparado se o max_attempts é null pois a quantidade de tentativas foi implementada posteriormente,
    // caso a composição não possua esse valor é considerado apenas se ele pode refazer o questionário e ignorado,
    // a verificação do limite de refações
    if (content.remake === "enable" && String(max_attempts) === 'null') {
      return true;
    }
    if (isNaN(parseInt(max_attempts)) === false && typeof parseInt(max_attempts) === 'number') {
      const default_first_attempt_count = 1;
      const total_attempts = parseInt(max_attempts) + default_first_attempt_count;
      return content.remake === "enable" && attempsMade < total_attempts
    }

    return false;
  }

  renderResult() {
    const {content, show_result, attempsMade} = this.state;
    const {
      max_attempts = null,
    } = content;

    if (show_result) {
      return (
        <div className="rdp-react-quiz-5-result">
          <span className="rdp-react-quiz-5-result-icon"/>
          {this.renderFinalMessage()}
          {this.userCanRemakeQuiz() && (
            <RemakeButtonContainer>
              <TextButton
                tabIndex={1}
                type="button"
                onClick={() => this.onRemakeQuestionnaire()}
                color={content.primary_color}
              >
                <RemakeButtonIcon>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    xlink="http://www.w3.org/1999/xlink"
                    version="1.1"
                    id="Layer_1"
                    x="0px"
                    y="0px"
                    width="32px"
                    height="32px"
                    viewBox="0 0 512 512"
                    enableBackground="new 0 0 512 512"
                    space="preserve"
                  >
                    <g>
                      <path
                        fill={
                          content.primary_color
                            ? content.primary_color
                            : "#111111"
                        }
                        d="M126.97,328.84c-5.84,0-11.63-2.42-15.78-7.15L5.17,200.31c-6.89-7.88-6.89-19.64,0-27.53L111.19,51.41   c7.61-8.7,20.83-9.59,29.53-1.99c8.71,7.6,9.59,20.82,2,29.52l-94,107.61l94,107.6c7.6,8.7,6.71,21.92-2,29.52   C136.75,327.14,131.85,328.84,126.97,328.84z"
                      />
                      <path
                        fill={
                          content.primary_color
                            ? content.primary_color
                            : "#111111"
                        }
                        d="M359.93,469.75H48.83c-11.56,0-20.93-9.37-20.93-20.93s9.37-20.93,20.93-20.93h311.1   c60.78,0,110.22-49.44,110.22-110.21s-49.44-110.21-110.22-110.21h-339C9.37,207.47,0,198.1,0,186.54   c0-11.55,9.37-20.92,20.93-20.92h339c83.85,0,152.07,68.22,152.07,152.06C512,401.53,443.77,469.75,359.93,469.75z"
                      />
                    </g>
                  </svg>
                </RemakeButtonIcon>

                <RemakeButtonText>
                  {this.getLabels().retry}
                </RemakeButtonText>
              </TextButton>
            </RemakeButtonContainer>
          )}
        </div>
      );
    }
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, "-").toLowerCase();
  }

  getNoteRange = () => {
    const {
      content: {
        grade_type = "numeral",
        grade_numeral = {
          min: 0,
          max: 10,
          steps: 1,
        },
      }
    } = this.state;
    const {min = 0, max = 10, steps = 1} = grade_numeral;

    switch (grade_type) {
      case 'numeral':
      default:
        let values = [];

        for (let i = min; i <= max; i += steps) {
          values.push(i);
        }

        return values;
    }
  }

  getUsersInteractions() {
    const {
      content,
      custom_options,
    } = this.state;
    const {
      register_uid,
      activity_uid,
    } = custom_options;

    return API.cms
      .learning
      .register.uid(register_uid)
      .interaction
      .recoverObjects({context_uid: content.id, activity_uid, last_occurrence: true}, custom_options);
  }

  render() {
    const {
      headerActionsTemplate,
      content: {
        primary_color,
        background_color,
        padding_bottom,
        padding_top,
        font_family,
        confirm_button_text_color,
        questions,
        id
      },
      className
    } = this.props;
    const {
      user: {
        roles = []
      },
      is_review_modal_open,
      interaction_objects,
      custom_options,
    } = this.state;

    return (
      <div
        id={this.props.content.id}
        ref={this.elementRef}
        className={`${className} ${
          this.getFont(font_family) || ""
        }`}
        style={{
          backgroundColor: background_color,
          paddingTop: padding_top + "px",
          paddingBottom: padding_bottom + "px",
        }}
      >
        {headerActionsTemplate}
        {this.renderContent()}
        {
          (isAdminReviewer(roles)) && (
            <ReviewModal
              open={is_review_modal_open}
              content={this.props.content}
              data={questions}
              customOptions={custom_options}
              users={interaction_objects}
              btnConfirmTextColor={confirm_button_text_color}
              primaryColor={primary_color}
              noteRange={this.getNoteRange()}
              onCancel={this.handleCloseReviewModal}
              onConfirm={this.handleCloseReviewModal}
              labels={this.getLabels()}
              updateLocalStudentInteraction={(interaction, selectedUser) => {
                let studentsCopy = [...interaction_objects];

                studentsCopy = studentsCopy.map(student => {
                  if (student.user_uid === selectedUser.uid) {
                    if (student.interaction_objects.length === 0) {
                      student.interaction_objects = [interaction];
                    } else {
                      student.interaction_objects = student.interaction_objects.map(row => {
                        if (row.uid === interaction.uid) {
                          return {
                            ...row,
                            score: interaction.score,
                            reviewed_at: interaction.reviewed_at,
                            reviewer_user_uid: interaction.reviewer_user_uid,
                            updated_at: interaction.updated_at,
                          };
                        }
                        return row;
                      });
                    }
                  }
                  return student;
                });

                this.setState({
                  interaction_objects: studentsCopy,
                })
              }}
            />
          )
        }
      </div>
    );
  }
}
