import React, {Component} from 'react';
import * as S from './element-code-snippet-1.styled';
import copyIcon from './copy.svg';
import checkCircleIcon from './check-circle.svg';
import {VisibleElement} from "../../utils/visible-element";

class ElementCodeSnippet1 extends Component {
  codeRef = React.createRef();
  elementRef = React.createRef();
  state = {
    copied: false,
    visible: false,
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.trackElementView();
  }

  trackElementView() {
    const content = this.props.content;
    const isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        const obj = {
          "action": "view",
          "element": "code-snippet-1",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title,
            "items": content.items
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        }

        this.props.onTrackEvent(obj);
      }
      this.setState({
        "visible": isVisible
      });
    }
  }

  getCaptionAriaProps = (item) => {
    const {
      id,
    } = item;

    return {
      'aria-labelledby': `caption-${id}`,
      'aria-describedby': `caption-${id}`,
    }
  }

  getLabels = (label) => {
    const {
      contentLanguage = "pt_br"
    } = this.props;
    const labels = {
      "pt_br": {
        "copy": 'Copiar',
      },
      "en": {
        "copy": 'Copy',
      }
    }

    const localizedLabels = labels[contentLanguage.toLowerCase()] || {};

    return localizedLabels[label] || label;
  }

  handleCopyItemCodeText = async (evt) => {
    evt.preventDefault();

    if (navigator && navigator.clipboard) {
      try {
        await navigator.clipboard.writeText(this.codeRef.current.innerText);
        this.setState({
          copied: true,
        });
      } catch (err) {
        console.error('Failed to copy text: ', err);
      }
    }
  }

  getFont = font => font ? font.replace(/\s/g, '-').toLowerCase() : "";

  render() {
    const {
      content,
      className,
    } = this.props;
    const {
      background_color,
      primary_color,
      padding_top,
      padding_bottom,
      font_family,
      items = [],
    } = content;
    const {
      copied,
    } = this.state;

    return (
      <S.ElementCodeSnippet1
        ref={this.elementRef}
        className={`${className} ${this.getFont(font_family)}`}
        backgroundColor={background_color}
        paddingTop={padding_top}
        paddingBottom={padding_bottom}
        primaryColor={primary_color}
      >
        <S.SnippetList>
          {items.map(item => (
            <S.SnippetItem
              key={item.id}
              className="element-snippet-1__snippet-item"
            >
              <S.CopyButton
                tabIndex="1"
                type="button"
                onClick={this.handleCopyItemCodeText}
                title={this.getLabels('copy')}
              >
                <S.CopyButtonIcon
                  src={copied ? checkCircleIcon : copyIcon}
                  width="24"
                  height="24"
                />
              </S.CopyButton>

              <S.Figure
                id={`figure-${item.id}`}
                {...this.getCaptionAriaProps(item)}
              >
                <S.Pre
                  ref={this.codeRef}
                  dangerouslySetInnerHTML={{__html: item.code}}
                />
              </S.Figure>
              {
                item.caption.length > 0 && (
                  <S.FigCaption
                    id={`caption-${item.id}`}
                    dangerouslySetInnerHTML={{__html: item.caption}}
                  />
                )
              }
            </S.SnippetItem>
          ))}
        </S.SnippetList>
      </S.ElementCodeSnippet1>
    );
  }
}

export default ElementCodeSnippet1;