import React, { Component } from 'react';
import ReactQuill from 'react-quill';
import {VisibleElement} from '../../utils/visible-element';
import {StringFormat} from '../../utils/string-format';
import './element-tabs.css';

export default class ElementTabs extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.state = {
      "content": [],
      "visible": false,
      "active_tab_content": null,
      "modules": {
        "toolbar": [
          ['bold', 'italic', 'underline','strike'],
          [{ 'align': '' }, { 'align': 'center' }, { 'align': 'right' }],
          [{'size': ["9px", "10px", "11px", "12px", "13px", "14px", "15px", "18px", "20px", "22px", "26px", "30px", "34px", "36px", "38px", "40px", "45px", "50px", "55px", "60px"]}],
          [{'list': 'ordered'}, {'list': 'bullet'}],
          ['link', {'color': ["#000000", "#e60000", "#ff9900", "#ffff00", "#008a00", "#0066cc", "#9933ff", "#ffffff", "#facccc", "#ffebcc", "#ffffcc", "#cce8cc", "#cce0f5", "#ebd6ff", "#bbbbbb", "#f06666", "#ffc266", "#ffff66", "#66b966", "#66a3e0", "#c285ff", "#888888", "#a10000", "#b26b00", "#b2b200", "#006100", "#0047b2", "#6b24b2", "#444444", "#5c0000", "#663d00", "#666600", "#003700", "custom-color"]}]
        ]
      },
      "formats": [
        'header',
        'size',
        'bold', 'italic', 'underline', 'strike',
        'list', 'bullet', 'indent', 'link', 'color', 'align'
      ]
    }

    this.handleChange = this.handleChange.bind(this);
  }

  componentDidUpdate() {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var items = content.items;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "tabs",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title,
            "options": this.getResumeItems(items)
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };
        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  getResumeItems(items) {
    var temp = [];
    for (var i = 0; i < items.length; i++) {
      temp.push({
        "id": items[i].id,
        "title": items[i].title,
        "text": StringFormat.HtmlToText(items[i].text),
        "image": items[i].image,
        "video": items[i].video
      });
    };
    return temp;
  }

  componentWillMount() {
    this.manipulateItems();

    const Font = ReactQuill.Quill.import('formats/font');
    Font.whitelist = ['large', 'medium', "small", "regular", "bold", "pullquote"];
    ReactQuill.Quill.register(Font, true);

    let SizeStyle = ReactQuill.Quill.import('attributors/style/size');
    SizeStyle.whitelist = ["9px", "10px", "11px", "12px", "13px", "14px", "15px", "18px", "20px", "22px", "26px", "30px", "34px", "36px", "38px", "40px", "45px", "50px", "55px", "60px"]
    ReactQuill.Quill.register(SizeStyle, true);
  }

  manipulateItems() {
    var content = this.props.content;
    this.setState({
      "content": content,
      "active_tab_content": content.items[0]
    })
  }

  handleChange(text) {
    var _this = this;
    var content = this.state.content;
    var items = content.items;
    var activeItem = this.state.active_tab_content;
    for (var i = 0; i < items.length; i++) {
      if (items[i].id === activeItem.id) {
        items[i].text = text;
        _this.setState({
          "active_tab_content": items[i]
        });
      };
    };

    this.setState({
      "content": content
    });
    this.props.onChange(content);
  }

  getImageUrl(image) {
    return image;
  }

  trackEvent(item) {
    var content = this.props.content;
    var items = content.items;
    var activeTab = this.state.active_tab_content;
    var obj = {
      "action": "click",
      "element": "tabs",
      "verb": "opened",
      "timestamp": Math.floor(Date.now() / 1000),
      "metadata": {
        "composition_uid": window.structure.metadata.content_uid,
        "element_id": content.id,
        "element_title": content.content_title,
        "active_tab_title": activeTab.title,
        "item_id": item.id,
        "options": this.getResumeItems(items)
      }
    };
    if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
      obj.assets = content.dam_assets;
    };

    this.props.onTrackEvent(obj);
  }

  onTabClick(item) {
    this.setState({
      "active_tab_content": item
    }, function() {
      this.trackEvent(item);
    })
  }

  renderQuill() {
    var modules = this.state.modules;
    var formats = this.state.formats;
    var content = this.state.active_tab_content;

    if (this.props.editable) {
      return <>
        <ReactQuill theme="bubble"
        value={content.text}
        modules={modules}
        formats={formats}
        onChange={(text) => this.handleChange(text)} />
      </>
    } else {
      return <div className="editor" dangerouslySetInnerHTML={{__html: content.text}}></div>
    }
  }

  getAssetUrl(asset) {
    return asset;
  }

  renderImage() {
    var content = this.state.active_tab_content;
    if (content.image !== null) {
      return <img className="element-tabs-item-image" src={this.getAssetUrl(content.image)}/>
    }
  }

  renderVideo() {
    var content = this.state.active_tab_content;
    if (content.video !== null) {
      return <video className="element-tabs-item-video" controls src={this.getAssetUrl(content.video)}/>
    }
  }

  renderActiveTabContent() {
    return <div className="element-tabs-content">
      {this.renderQuill()}
      {this.renderImage()}
      {this.renderVideo()}
    </div>
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  render() {
    var _this = this;
    let { headerActionsTemplate, content } = this.props;
    var accordionStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }
    var items = this.state.content.items;

    return <div id={content.id} className={`${this.props.className} ${this.getFont(content.font_family) || ""}`} style={accordionStyle}>
      {headerActionsTemplate}
      <div ref={this.elementRef} className="element-tabs-container">
        <ul className="element-tabs-items">
          {items.map(function(item, index) {
            return <li key={index} style={_this.state.active_tab_content.id === item.id ? {borderColor: content.active_color} : {}} className={`element-tabs-item ${_this.state.active_tab_content.id === item.id ? "active" : ""}`}>
              <span className="element-tabs-item-title" style={_this.state.active_tab_content.id === item.id ? {color: content.active_color} : {}} onClick={() => _this.onTabClick(item)}>{item.title}</span>
            </li>
          })}
        </ul>
        {_this.renderActiveTabContent()}
      </div>
    </div>
  }
}
