import React, {useEffect, useMemo, useRef, useState} from 'react';
import KeyCode from "../../utils/key-code";
import * as S from "./review-modal.styled";
import searchIcon from "../../assets/svg/icon-search.svg";
import searchClearIcon from "../../assets/svg/close.svg";


function ReviewModal(props) {
  const {
    open = false,
    data = [],
    users = [],
    onConfirm = (text) => text,
    onCancel = () => {
    },
    value = "",
    primaryColor = "#000",
    labels = {},
    closeIcon,
    expandRightIcon,
    expandLeftIcon,
    iterateRightList = (item, index, selectedUser) => item,
  } = props;
  const closeRef = useRef();
  const rightListRef = useRef();
  const [text, setText] = useState(value);
  const [searchText, setSearchText] = useState("");
  const [selectedUser, setSelectedUser] = useState({});
  const [isListOfItemsOpened, setIsListOfItemsOpened] = useState(false);
  const [messages, setMessages] = useState(data);
  const [sortAnsweredOrder, setSortAnsweredOrder] = useState("");
  const [sortReviewedOrder, setSortReviewedOrder] = useState("");

  const interactionObject = (user = {}, index) => {
    const {
      user_uid = index,
      name = `Usuário ${index + 1}`,
      email = "",
      interaction_objects = [],
      interaction_uid = "",
    } = user;

    let count_reviewed = 0;
    let total_messages = 0;
    let answered = false;

    let interactionObjectTemp = messages.map(message => {
      let interaction = {};
      let isReviewed = false;

      const {children = []} = message;

      if (children.length > 0) {
        children.forEach(child => {
          interaction = interaction_objects.find(interaction => interaction.target_uid === child.uid);

          if (interaction) {
            const {
              reviewer_uid = null,
              score = null,
            } = interaction;

            total_messages++;
            answered = true;

            if (((score !== null && score !== "" && score !== "null") || reviewer_uid !== null)) {
              isReviewed = true;
              count_reviewed++;
            }
          }
        })
      }

      if (interaction_objects) {
        interaction = interaction_objects.find(interaction => interaction.target_uid === message.uid);

        if (interaction) {
          const {
            uid = null,
            reviewer_uid = null,
            object_data,
            score = null,
            target_uid = null,
            context_uid = null,
          } = interaction;

          total_messages++;
          answered = true;

          const {
            data = ""
          } = object_data;

          if (((score !== null && score !== "" && score !== "null") || reviewer_uid !== null)) {
            isReviewed = true;
            count_reviewed++;
          }
          return {
            uid,
            context_uid,
            reviewed: isReviewed,
            answered,
            score,
            answer: data,
            target_uid,
          }
        }
      }

      return {
        reviewed: false,
        answered,
        score: null,
        answer: "",
        target_uid: null,
      }
    })

    return {
      uid: user_uid,
      name,
      email,
      interaction_uid,
      count_reviewed,
      total_messages,
      answered,
      interaction_objects: interactionObjectTemp,
    }
  }

  const byAnswered = (order = "asc") => ( a, b ) => {
    if ( order === "asc" ) {
      if ( a.answered && !b.answered ) {
        return -1;
      }
      if ( !a.answered && b.answered ) {
        return 1;
      }
    } else if ( order === "desc" ) {
      if ( !a.answered && b.answered ) {
        return -1;
      }
      if ( a.answered && !b.answered ) {
        return 1;
      }
    }
    return 0;
  }

  const byReviewed = (order = "asc") => ( a, b ) => {
    if ( order === "asc" ) {
      if ( a.total_messages !== a.count_reviewed && b.total_messages === b.count_reviewed ) {
        return -1;
      }
      if ( a.total_messages === a.count_reviewed && b.total_messages !== b.count_reviewed ) {
        return 1;
      }
    } else if (order === "desc") {
      if ( a.total_messages === a.count_reviewed && b.total_messages !== b.count_reviewed ) {
        return -1;
      }
      if ( a.total_messages !== a.count_reviewed && b.total_messages === b.count_reviewed ) {
        return 1;
      }
    }
    return 0;
  }

  const students = useMemo(() =>
      users.map(interactionObject)
        .sort(byAnswered(sortAnsweredOrder))
        .sort(byReviewed(sortReviewedOrder)),
    [users, sortAnsweredOrder, sortReviewedOrder]
  );

  useEffect(() => {
    setMessages(data);
  }, [data]);

  useEffect(() => {
    if (rightListRef && rightListRef.current) {
      rightListRef.current.scrollTop = 0;
    }
    if (closeRef && closeRef.current) {
      closeRef.current.focus();
    }
    setSelectedUser({});
  }, [open]);

  const handleEscape = (event) => {
    let key = event.which || event.keyCode;

    if (key === KeyCode.ESC) {
      event.stopPropagation();
      onCancel();
    }
  }

  const handleSubmit = event => {
    event.preventDefault();
    onConfirm(text);
    setText("");
  }

  const handleClose = () => {
    onCancel();
    setText("");
  }

  const handleStudentSelect = (user) => {
    setSelectedUser(user);
    updateLocalQuestion(user.interaction_objects);
    setIsListOfItemsOpened(false);
    if (rightListRef && rightListRef.current) {
      rightListRef.current.scrollTop = 0;
    }
  }

  const handleStudentsMenu = () => {
    setIsListOfItemsOpened(!isListOfItemsOpened);
  }

  const updateLocalQuestion = (interaction) => {
    if (Array.isArray(interaction)) {
      let messagesCopy = [...messages];
      messagesCopy.forEach(message => {
        const userInteractionObject = interaction.find(item => item.target_uid === message.id);
        if (userInteractionObject) {
          message.score = userInteractionObject.score;
        }
      });

      setMessages(messagesCopy);
    } else {
      let messagesCopy = [...messages];
      messagesCopy.forEach(question => {
        if (question.id === interaction.target_uid) {
          question.score = interaction.score;
        }
      });
      setMessages(messagesCopy);
    }
    return interaction;
  }

  const iconExpandLeft = () => {
    return expandRightIcon || (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        version="1.1"
        width="20"
        height="20"
        viewBox="0 0 24 24"
      >
        <path d="M4,2H2V22H4V13H18.17L12.67,18.5L14.08,19.92L22,12L14.08,4.08L12.67,5.5L18.17,11H4V2Z"/>
      </svg>
    )
  }

  const iconExpandRight = () => {
    return expandLeftIcon || (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        version="1.1"
        width="20"
        height="20"
        viewBox="0 0 24 24"
      >
        <path d="M20,22H22V2H20V11H5.83L11.33,5.5L9.92,4.08L2,12L9.92,19.92L11.33,18.5L5.83,13H20V22Z"/>
      </svg>
    )
  }

  const iconClose = () => {
    return closeIcon || (
      <svg
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        width="20"
        height="20"
        viewBox="0 0 768 768">
        <path
          d="M607.5 205.5l-178.5 178.5 178.5 178.5-45 45-178.5-178.5-178.5 178.5-45-45 178.5-178.5-178.5-178.5 45-45 178.5 178.5 178.5-178.5z"/>
      </svg>
    )
  }

  const handleSearch = (event) => {
    const {value} = event.target;
    setSearchText(value);
  }

  const sameUserNameOrEmail = (user = {}) => {
    if (searchText) {
      const {name, email} = user;
      const text = searchText.toLowerCase();
      return name.toLowerCase().includes(text) || email.toLowerCase().includes(text);
    } else {
      return true;
    }
  }

  const handleClearSearch = () => {
    setSearchText("");
  }

  const interactionUserName = (user) => {
    const {
      name,
      email,
    } = user;
    return {
      name,
      email,
    }
  }

  const handleSortByAnswered = () => {
    if (sortAnsweredOrder === "asc") {
      setSortAnsweredOrder("desc");
    } else if (sortAnsweredOrder === "desc") {
      setSortAnsweredOrder("");
    } else {
      setSortAnsweredOrder("asc");
    }
  }

  const handleSortByReviewed = () => {
    if (sortReviewedOrder === "asc") {
      setSortReviewedOrder("desc");
    } else if (sortReviewedOrder === "desc") {
      setSortReviewedOrder("");
    } else {
      setSortReviewedOrder("asc");
    }
  }

  const getStyleForOrder = (order) => {
    if (order === "asc") {
      return "overline"
    }
    if (order === "desc") {
      return "underline"
    }
    return "none"
  }

  return (<S.ModalDiv
    role="dialog"
    aria-modal="true"
    aria-labelledby={labels.review || "Revisar"}
    aria-describedby={labels.review || "Revisar"}
    onKeyUp={handleEscape}
    hidden={!open}
  >
    <S.Form onSubmit={handleSubmit}>
      <S.Overlay onClick={handleClose}/>

      <S.ContentDiv>
        <S.ReviewContent>
          <S.MenuIcon
            tabIndex="1"
            type="button"
            onClick={handleStudentsMenu}
            primaryColor={primaryColor}
          >
            {
              isListOfItemsOpened
                ? iconExpandRight()
                : iconExpandLeft()
            }
          </S.MenuIcon>

          <S.CloseButton
            ref={closeRef}
            className={isListOfItemsOpened ? "visible" : ""}
            tabIndex="1"
            type="button"
            onClick={handleClose}
            primaryColor={primaryColor}
            title={labels.close || "Fechar"}
          >
            {iconClose()}
          </S.CloseButton>

          <S.Grid className={isListOfItemsOpened ? "visible" : ""}>
            <S.HeadRow className={isListOfItemsOpened ? "visible" : ""}>
              <S.SearchColumn>
                <S.SearchLabel
                  aria-label={labels.search || "Buscar"}
                >
                  <S.SearchInput
                    type="text"
                    name="search"
                    placeholder={labels.search || "Buscar"}
                    primaryColor={primaryColor}
                    value={searchText}
                    onChange={handleSearch}
                  />

                  {
                    searchText.length
                      ? (
                        <S.SearchInputIcon
                          role="button"
                          src={searchClearIcon}
                          onClick={handleClearSearch}
                        />
                      ) : (
                        <S.SearchInputIcon
                          role="button"
                          src={searchIcon}
                        />
                      )
                  }

                </S.SearchLabel>
              </S.SearchColumn>

              <S.AnsweredColumn
                type="button"
                background={primaryColor}
                onClick={handleSortByAnswered}
              >
                <p
                  style={{color: primaryColor, textDecoration: getStyleForOrder(sortAnsweredOrder)}}
                >
                  {labels.answered || "Respondido"}
                </p>
              </S.AnsweredColumn>

              <S.ReviewedColumn
                type="button"
                background={primaryColor}
                onClick={handleSortByReviewed}
              >
                <p
                  style={{color: primaryColor, textDecoration: getStyleForOrder(sortReviewedOrder)}}>
                  {labels.reviewed || "Corrigido"}
                </p>
              </S.ReviewedColumn>
            </S.HeadRow>

            <S.ReviewColumn>
              <S.Title primaryColor={primaryColor}>
                {interactionUserName(selectedUser).name}
              </S.Title>
            </S.ReviewColumn>
          </S.Grid>

          <S.Grid className={isListOfItemsOpened ? "visible" : ""}>
            <S.LeftList>
              {
                students
                  .filter(sameUserNameOrEmail)
                  .map((user, index) => (
                    <S.Student key={index}>
                      <S.StudentButton
                        tabIndex="1"
                        type="button"
                        onClick={() => handleStudentSelect(user)}
                        aria-selected={user.uid === selectedUser.uid}
                        primaryColor={primaryColor}
                      >
                        <S.BodyRow className={isListOfItemsOpened ? "visible" : ""}>
                          <S.StudentData>
                            <p style={{fontSize: "16px"}}>{user.name}</p>
                            <p style={{fontSize: "16px"}}>{user.email}</p>
                          </S.StudentData>

                          <S.AnsweredData>
                            {
                              (user.interaction_objects.find(objects => objects.answered === true)) && (
                                <S.CheckboxLabel
                                  aria-label={labels.answered_by_student || "Respondido pelo aluno"}
                                  checkColor={primaryColor}
                                >
                                  <S.CheckboxInput
                                    type="checkbox"
                                    readOnly
                                    checked
                                  />
                                  <S.Checkmark
                                    className="checkmark"
                                  />
                                </S.CheckboxLabel>
                              )
                            }
                          </S.AnsweredData>

                          <S.ReviewedData>
                            {
                              (user.count_reviewed > 0 && user.count_reviewed === user.total_messages) ? (
                                <S.CheckboxLabel
                                  aria-label={labels.reviewed || "Corrigido"}
                                  checkColor={primaryColor}
                                >
                                  <S.CheckboxInput
                                    type="checkbox"
                                    readOnly
                                    checked
                                  />
                                  <S.Checkmark className="checkmark"/>
                                </S.CheckboxLabel>
                              ) : (
                                <p>{user.count_reviewed}/{user.total_messages || 0}</p>
                              )
                            }
                          </S.ReviewedData>
                        </S.BodyRow>
                      </S.StudentButton>
                    </S.Student>
                  ))
              }
            </S.LeftList>

            <S.ReviewData>
              <S.ReviewDataOverlay
                isVisible={Object.keys(selectedUser).length === 0}
              >
                <p style={{color: primaryColor}}>
                  {labels.select_to_start_review || "Selecione um aluno para efetuar a revisão."}
                </p>
              </S.ReviewDataOverlay>

              <S.ReviewNoMessagesDataOverlay
                isVisible={selectedUser.total_messages === 0}
              >
                <p style={{color: primaryColor}}>
                  {labels.no_messages_to_review || "Este aluno não possui mensagens para revisão."}
                </p>
              </S.ReviewNoMessagesDataOverlay>

              <S.RightList ref={rightListRef}>
                {messages.map((message, index) => <span
                  key={index}>{iterateRightList(message, index, selectedUser)}</span>)}
              </S.RightList>
            </S.ReviewData>
          </S.Grid>
        </S.ReviewContent>
      </S.ContentDiv>
    </S.Form>
  </S.ModalDiv>)
}

export default ReviewModal;