import React, { Component } from 'react';
import {VisibleElement} from '../../utils/visible-element';
import { AzureMP } from '../../libs/react-azure-mp/index';
import './element-video.css';

export default class ElementVideo extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.state = {
      "visible": false,
      "azure_video_url": null,
      "azure_url": null,
      "azure_mime_type": null,
      "azure_subtitles": null,
      "render_azure_player": window.location.search.indexOf("?custom_options=") > -1,
      custom_options: window.cstmptns,
    }
  }

  componentDidUpdate() {
    this.trackElementView();
  }

  componentDidMount() {
    this.getCaptions();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "video",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title,
            "url": content.video
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };

        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  trackEvent(verb, current, duration) {
    var content = this.props.content;
    var obj = {
      "action": "click",
      "element": "video",
      "verb": verb,
      "timestamp": Math.floor(Date.now() / 1000),
      "metadata": {
        "composition_uid": window.structure.metadata.content_uid,
        "element_id": content.id,
        "element_title": content.content_title,
        "url": content.video,
        "time": {
          "current": current,
          "duration": duration,
          "progress": current / duration * 100
        }
      }
    }
    this.props.onTrackEvent(obj);
  }

  onVideoPlay(e) {
    this.trackEvent("played", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  onVideoPause(e) {
    this.trackEvent("paused", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  onVideoEnded(e) {
    this.trackEvent("ended", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  onVideoSeeking(e) {
    this.trackEvent("skipped", e.currentTarget.currentTime, e.currentTarget.duration)
  }

  getVideoUrl(video) 
  {
    if (video.indexOf("http") == -1)
    {
      return `./${video}`;
    }
    
    const { custom_options } = this.state;

    if (!custom_options) 
    {
      return video;
    }
    const {
        api_env,
        project_key,
        access_token,
        domain 
      } = custom_options;
    return `https://${domain}/storage/v1.0/content/default/target?uid=${video.split("uid=")[1].split("&")[0]}&access_token=${access_token}&project_key=${project_key}&api_env=${api_env}`;
  }

  async getCaptions() {
    if (this.state.render_azure_player) {
      var manifestUrl;
      var videoUrl = this.props.content.video;
      var videoObj = {
        "video_url": videoUrl,
        "url": null,
        "mime_type": null,
        "subtitles": null
      }

      if (videoUrl.search("/content/default/target") > -1) {
        manifestUrl = videoUrl.replace("/content/default/target", "/content/path/manifest.json");
      } else {
        manifestUrl = videoUrl.replace("/content", "/content/path/manifest.json");
      }

      var manifest = await fetch(manifestUrl).then((p) => p.json());
      var subtitles = [];

      if (manifest.subtitles) {
        var data = manifest.subtitles.map((p) => {
          p.asset = manifest.assets.find((a) => a.uid == p.asset_uid);
          return p;
        });

        subtitles = data
          .filter((p) => p.asset.mime_type == "text/vtt")
          .map((p) => {
            var result = {
              src: videoUrl.replace("/content",`/content/path/${p.asset.uri}`).replace("/default/target", ""),
              srclang: p.language_code,
              kind: "subtitles",
              label: p.language,
            };

            return result;
          });
      }

      var loadDefaultFormat = true;

      if (
        manifest.azure_media_options &&
        manifest.azure_media_options["SmoothStreaming"]
      ) {
        var tempUrl = manifest.azure_media_options["SmoothStreaming"][0];
        var streamInfo = await fetch(tempUrl, { method: "head" });

        if (streamInfo.ok) {
          videoObj.mime_type = streamInfo.headers.get("Content-Type");
          videoObj.video_url = tempUrl;
          loadDefaultFormat = false;
        }
      }

      if (loadDefaultFormat) 
      {
        if (videoUrl.indexOf('default/target') != -1) {
          videoUrl = videoUrl.replace("/content/default/target",`/content/path/${manifest.default_target}`);
        }else
        {
          videoUrl = videoUrl.replace("/content",`/content/path/${manifest.default_target}`);
        }
        videoObj.mime_type = manifest.type;
        videoObj.video_url = videoUrl;
      }

      videoObj.subtitles = subtitles;

      this.setState({
        "azure_subtitles": videoObj.subtitles,
        "azure_video_url": videoObj.video_url,
        "azure_url": videoObj.url || videoObj.video_url,
        "azure_mime_type": videoObj.mime_type
      });
    }
  }

  render() {
    var content = this.props.content;
    var imageStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }

    return (<div id={content.id} ref={this.elementRef} className={this.props.className} style={imageStyle}>
      {!this.state.render_azure_player &&
        <video
          controls src={this.getVideoUrl(content.video)}
          onPlay={(e) => this.onVideoPlay(e)}
          onPause={(e) => this.onVideoPause(e)}
          onEnded={(e) => this.onVideoEnded(e)}
          onSeeking={(e) => this.onVideoSeeking(e)}
        />
      }
      {this.state.render_azure_player && this.state.azure_video_url &&
        <div className="element-video-1-azure">
          <AzureMP
            className="azuremediaplayer amp-default-skin amp-big-play-centered"
            src={[{src: this.state.azure_video_url, type: this.state.azure_mime_type }]}
            tracks={this.state.azure_subtitles}
            options={{
              autoplay: false,
              controls: true,
              width: "760px",
              logo: { enabled: false },
              adaptRatio: false,
              playbackSpeed: {
                enabled: true,
                initialSpeed: 1.0,
                speedLevels: [
                  { name: "x2.0", value: 2.0 },
                  { name: "x1.75", value: 1.75 },
                  { name: "x1.5", value: 1.5 },
                  { name: "x1.25", value: 1.25 },
                  { name: "normal", value: 1.0 },
                  { name: "x0.75", value: 0.75 },
                  { name: "x0.5", value: 0.5 },
                ],
              },
            }}
          />
        </div>
      }
    </div>)
  }
}
