import React, { Component } from 'react';
import {VisibleElement} from '../../utils/visible-element';
import {StringFormat} from '../../utils/string-format';
import './element-accordion.css';

export default class ElementAccordion extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.state = {
      "content": [],
      "visible": false
    }
  }

  componentDidUpdate() {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var items = content.items;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": content.type,
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title,
            "options": this.getResumeItems(items)
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };
        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  trackEvent(index) {
    var content = this.state.content;
    var items = content.items;
    var item = items[index];
    var obj = {
      "action": "click",
      "element": "accordion",
      "verb": item.expanded? "opened" : "closed",
      "timestamp": Math.floor(Date.now() / 1000),
      "metadata": {
        "composition_uid": window.structure.metadata.content_uid,
        "element_id": content.id,
        "element_title": content.content_title,
        "active_accordion_title": item.title,
        "item_id": item.id,
        "options": this.getResumeItems(items)
      }
    }
    this.props.onTrackEvent(obj);
  }

  getResumeItems(items) {
    var temp = [];
    for (var i = 0; i < items.length; i++) {
      temp.push({
        "id": items[i].id,
        "title": items[i].title,
        "text": StringFormat.HtmlToText(items[i].text),
        "image": items[i].image,
        "video": items[i].video
      });
    };
    return temp;
  }

  componentWillMount() {
    this.manipulateItems();
  }

  manipulateItems() {
    var content = this.props.content;
    var items = content.items;
    for (var i = 0; i < items.length; i++) {
      items[i].expanded = false;
    };
    content.items = items;
    this.setState({
      "content": content
    })
  }

  onToggleItem(index) {
    var content = this.state.content;
    var items = content.items;
    if(content.behavior === "close-all") {
      for (var i = 0; i < items.length; i++) {
        if (i !== index) {
          items[i].expanded = false;
        }
      };
    };
    items[index].expanded = !items[index].expanded;
    this.setState({
      "content": content
    }, function() {
      this.trackEvent(index);
    })
  }

  renderQuill(item, index) {
    return <div className="editor" dangerouslySetInnerHTML={{__html: item.text}}></div>
  }

  getAssetUrl(asset) {
    return asset;
  }

  renderImage(item) {
    if (item.image !== null) {
      return <img className="element-accordion-item-image" src={this.getAssetUrl(item.image)}/>
    }
  }

  renderVideo(item) {
    if (item.video !== null) {
      return <video className="element-accordion-item-video" controls src={this.getAssetUrl(item.video)}/>
    }
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  render() {
    var _this = this;
    let { headerActionsTemplate, content } = this.props;
    var accordionStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }
    var items = this.state.content.items;

    return <div id={content.id} ref={this.elementRef} className={`${this.props.className} ${this.getFont(content.font_family) || ""}`} style={accordionStyle}>
      {headerActionsTemplate}
      <ul className="element-accordion-items">
        {items.map(function(item, index) {
          return <li key={index} className={`element-accordion-item ${item.expanded === true ? "expanded" : "not-expanded"}`}>
            <span className="element-accordion-item-title" onClick={() => _this.onToggleItem(index)}>{item.title}</span>
            <div className="element-accordion-item-text">
              {_this.renderQuill(item, index)}
            </div>
            {_this.renderImage(item)}
            {_this.renderVideo(item)}
          </li>
        })}
      </ul>
    </div>
  }
}
