import React, { Component } from 'react';
import 'animate.css/animate.min.css';
import './element-image-with-text.css';
import {VisibleElement} from "../../utils/visible-element";

export default class ElementImageWithText extends Component {
  constructor(props) {
    super(props);

    this.elementRef = React.createRef();

    this.state = {
      "visible": false
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "image-with-text",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };

        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  getImageUrl(image) {
    return image;
  }

  handleTextChange(text) {
    var content = this.props.content;
    content.text = text;
    this.props.onChange(content);
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  render() {
    let { headerActionsTemplate, footerActionsTemplate, content } = this.props;
    var containerStyle = {
      backgroundColor: content.background_color,
      paddingTop: content.padding_top + "px",
      paddingBottom: content.padding_bottom + "px"
    }

    return <div ref={this.elementRef} className={`element-image-with-text ${this.getFont(content.font_family) || ""}`} style={containerStyle}>
      {headerActionsTemplate}
      <div className="element-image-with-text-container" style={{
        backgroundImage: `url(${this.getImageUrl(content.image)})`
      }}>
        <div className="element-image-with-text-container-opacity" style={{
          opacity: content.opacity,
          backgroundColor: content.opacity_color
        }}></div>
        <div className="element-image-with-text-container-center">
          <div className="element-image-with-text-container-line">
            <div className="element-image-with-text-container-divider" style={{backgroundColor: content.divider_color}}></div>
          </div>
          <div className="element-image-with-text-container-text">
            <div className="editor" dangerouslySetInnerHTML={{__html: content.text}}></div>
          </div>
        </div>
      </div>
      {footerActionsTemplate}
    </div>
  }
}
