import React, {useEffect} from 'react';
import KeyCode from "../../utils/key-code";
import {
  ActionsDiv,
  Button,
  Container,
  ContentDiv,
  Description,
  DescriptionText,
  Label,
  Backdrop
} from "./alert-dialog.styled";

function AlertDialog(props) {
  const {
    id = "alert-dialog",
    open = false,
    onConfirm = () => {
    },
    onCancel = () => {
    },
    label = "Confirmação",
    description = "Deseja realizar esta ação?",
    btnConfirmLabel = "Sim",
    btnCancelLabel = "Não",
    controlsID = "",
    btnConfirmTextColor = "#fff",
    btnConfirmColor = "#000",
    btnCancelTextColor = "#fff",
    btnCancelColor = "#000",
  } = props;
  const [isLoading, setIsLoading] = React.useState(false);

  useEffect(() => {
    if (open) {
      document.querySelector('body').style.overflow = "hidden";
    } else {
      document.querySelector('body').style.overflow = "auto";
    }

    return () => document.querySelector('body').style.overflow = "auto";
  }, [open])

  const handleEscape = (event) => {
    let key = event.which || event.keyCode;

    if (key === KeyCode.ESC) {
      event.stopPropagation();
      onCancel();
    }
  }

  const handleCancel = () => {
    onCancel();
  }

  const handleConfirm = () => {
    onConfirm();
  }

  return (
    <Backdrop hidden={!open}>
      <Container
        id={id}
        role="alertdialog"
        aria-modal="true"
        aria-labelledby={id + "-label"}
        aria-describedby={id + "-desc"}
        onKeyUp={handleEscape}
        hidden={!open}
      >


        <ContentDiv>
          <Label id={id + "-label"}>
            {label}
          </Label>

          <Description id={id + "-desc"}>
            <DescriptionText>
              {description}
            </DescriptionText>
          </Description>

          <ActionsDiv>
            <Button
              id={id + "-confirm"}
              aria-controls={controlsID}
              type="button"
              onClick={handleConfirm}
              color={btnConfirmTextColor}
              background={btnConfirmColor}
              disabled={isLoading}
            >
              {btnConfirmLabel}
            </Button>

            <Button
              type="button"
              onClick={handleCancel}
              style={{marginLeft: "15px"}}
              color={btnCancelTextColor}
              background={btnCancelColor}
              disabled={isLoading}
            >
              {btnCancelLabel}
            </Button>
          </ActionsDiv>
        </ContentDiv>
      </Container>
    </Backdrop>
  )
}

export default AlertDialog;