import React, { Component } from 'react';
import './element-quote-1.css';
import {VisibleElement} from "../../utils/visible-element";

export default class ElementQuote1 extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.state = {
      "avatar_size": "width",
      "visible": false
    }

    this.handleChange = this.handleChange.bind(this);
    this.handleChangeText = this.handleChangeText.bind(this);
    this.handleChangeAuthorName = this.handleChangeAuthorName.bind(this);
  }

  componentDidMount() {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "quote-1",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };

        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  handleChange(content) {
    this.props.onChange(content);
  }

  handleAvatarChange(img) {
    this.setState({
      "avatar_size": img.target.width >= img.target.height ? "height" : "width"
    });
  }

  handleChangeText(text) {
    var content = this.props.content;
    content.text = text;
    this.props.onChange(content);
  }

  renderText() {
    let { content } = this.props;

    return <div className="rdp-element-quote-1-text-container">
      <div className="rdp-element-quote-1-text">
        <div className="editor" dangerouslySetInnerHTML={{__html: content.text}}/>
      </div>
    </div>;
  }

  renderAvatar() {
    let { content } = this.props;

    if (content.avatar !== null) {
      var avatarSize = this.state.avatar_size;
      return <div className="rdp-element-quote-1-avatar">
        <img
          style={avatarSize === "width" ? { width: "102%" } : { height: "102%" }}
          src={this.getAvatarUrl(content.avatar)}
          onLoad={this.handleAvatarChange.bind(this)}
        />
      </div>;
    } else {
      return null;
    }
  }

  getAvatarUrl(image) {
    return image;
  }

  handleChangeAuthorName(name) {
    var content = this.props.content;
    content.author_name = name;
    this.props.onChange(content);
  }

  renderAuthorInfo() {
    let { content } = this.props;

    var modules = {
      toolbar: [
        ['bold', 'italic', 'underline','strike'], [{'color': ["#000000", "#e60000", "#ff9900", "#ffff00", "#008a00", "#0066cc", "#9933ff", "#ffffff", "#facccc", "#ffebcc", "#ffffcc", "#cce8cc", "#cce0f1", "#ebd6ff", "#bbbbbb", "#f06666", "#ffc266", "#ffff66", "#66b966", "#66a3e0", "#c281ff", "#888888", "#a10000", "#b26b00", "#b2b200", "#006100", "#0047b2", "#6b24b2", "#444444", "#1c0000", "#663d00", "#666600", "#003700", "#002966", "#3d1466", 'custom-color']}]
      ],
    };
    var formats = ['bold', 'italic', 'underline', 'strike', 'color'];
    
    return <div className="rdp-element-quote-1-author-info">
      <div className="rdp-element-quote-1-author-name">
        <div className="editor"
          style={{fontFamily: "Merriweather-Regular"}}
          dangerouslySetInnerHTML={{__html: content.author_name}}
        />
      </div>
    </div>
  }

  getBackgroundUrl(image) {
    return image;
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  render() {
    let { headerActionsTemplate, footerActionsTemplate, content } = this.props;
    
    return <div ref={this.elementRef} className={`rdp-element-quote-1 ${this.getFont(content.font_family) || ""}`} style={{ backgroundColor: content.background_color }}>
      {headerActionsTemplate}
      <div className="rdp-element-quote-1-container" style={{borderColor: content.primary_color}}>
        {this.renderAvatar()}
        {this.renderText()}
        {this.renderAuthorInfo()}
      </div>
      {footerActionsTemplate}
    </div>;
  }
}
