import React, {Component} from 'react';
import * as S from "./element-button-1.styled";
import {VisibleElement} from "../../utils/visible-element";

class ElementButton1 extends Component {
  elementRef = React.createRef();
  state = {
    content: this.props.content,
    visible: false,
  }

  componentDidMount() {
    this.trackElementView();
  }

  trackElementView = () => {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          action: "view",
          element: content.type,
          timestamp: Math.floor(Date.now() / 1000),
          metadata: {
            composition_uid: window.structure.metadata.content_uid,
            element_id: content.id,
            element_title: content.content_title,
          },
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        }

        this.props.onTrackEvent(obj);
      }
      this.setState({
        visible: isVisible,
      });
    }
  }

  getFont = (font) => font ? font.replace(/\s/g, '-').toLowerCase() : "";

  getDestinationUrl = (item) => {
    switch (item.type) {
      case "link":
        return item.destination;
      case "email":
        return `mailto:${item.destination}`;
      default:
        return item.destination;
    }
  }

  render() {
    const {
      headerActionsTemplate,
      footerActionsTemplate,
      content,
      className
    } = this.props;
    const {
      background_color,
      primary_color,
      padding_top,
      padding_bottom,
      font_family,
      items = [],
    } = content;

    return (
      <S.ElementButton1
        ref={this.elementRef}
        className={`${className} ${this.getFont(font_family) || ""}`}
        backgroundColor={background_color}
        paddingTop={padding_top}
        paddingBottom={padding_bottom}
      >
        {headerActionsTemplate}

        <S.ButtonList>
          {items.map(item => (
            <S.ButtonItem
              className="element-button-1__button-item"
              key={item.id}
            >
              <S.ButtonLabel
                htmlFor={item.id}
                dangerouslySetInnerHTML={{__html: item.description}}
              />

              <S.ButtonLink
                className="element-button-1__button"
                id={item.id}
                tabIndex="1"
                href={this.getDestinationUrl(item)}
                target="_blank"
                primaryColor={primary_color}
              >
                {item.label}
              </S.ButtonLink>
            </S.ButtonItem>
          ))}
        </S.ButtonList>

        {footerActionsTemplate}
      </S.ElementButton1>
    );
  }
}

export default ElementButton1;