import React, {Component} from 'react';
import * as S from "./element-timeline-1.styled";
import {VisibleElement} from "../../utils/visible-element";


class ElementTimeline1 extends Component {
  elementRef = React.createRef();
  state = {
    visible: false,
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.trackElementView();
  }

  trackElementView() {
    const content = this.props.content;
    const isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        const obj = {
          "action": "view",
          "element": "timeline-1",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title,
            "items": content.items
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        }

        this.props.onTrackEvent(obj);
      }
      this.setState({
        "visible": isVisible
      });
    }
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
    return "";
  }

  getAssetUrl(assetUrl) {
    return assetUrl;
  }

  render() {
    const {
      className,
      content,
      headerActionsTemplate,
      footerActionsTemplate,
    } = this.props;
    const {
      background_color,
      primary_color,
      padding_top,
      padding_bottom,
      items = [],
    } = content;

    return (
      <S.ElementTimeline1
        ref={this.elementRef}
        className={`${className} ${this.getFont()}`}
        backgroundColor={background_color}
        paddingTop={padding_top}
        paddingBottom={padding_bottom}
      >
        {headerActionsTemplate}

        <S.TimelineList>
          {items.map((item) => (
            <S.TimelineItem
              key={item.id}
              backgroundColor={background_color}
              primaryColor={primary_color}
            >
              <S.TimelineDate
                primaryColor={primary_color}
              >
                {item.date}
              </S.TimelineDate>

              <S.TimelineTitle
                dangerouslySetInnerHTML={{__html: item.title}}
              />

              {item.audio && (
                <S.TimelineAudio
                  src={this.getAssetUrl(item.audio)}
                  controls
                />
              )}

              <S.TimelineDescription
                dangerouslySetInnerHTML={{__html: item.description}}
              />

              {item.image && (
                <S.TimelineImage
                  src={this.getAssetUrl(item.image)}
                />
              )}
            </S.TimelineItem>
          ))}
        </S.TimelineList>

        {footerActionsTemplate}
      </S.ElementTimeline1>
    );
  }
}

export default ElementTimeline1;