import styled, {css} from "styled-components";

const Container = styled.div`
  --note-dropdown-primary-color: #00643e;
  --note-dropdown-background-color: #fff;
  --note-dropdown-background-hover-color: rgba(213, 213, 213, 0.49);
  --note-dropdown-list-z-index: 1;
	
  display: flex;
  justify-content: center;
  align-items: center;
`

const Dropdown = styled.div`
	position: relative;
`

const DropdownButton = styled.button`
  background-color: transparent;
  border: none;
  color: ${props => props.color || "var(--note-dropdown-primary-color)"};
  text-transform: uppercase;
  font-size: 10px;
  border-radius: 10px;
  cursor: pointer;
  display: inline-flex;
	
	&:focus {
    outline: 2px solid ${props => props.color || "var(--note-dropdown-primary-color)"} !important;
    outline-offset: 2px;
	}
	
	&:hover {
    filter: saturate(1.5);
	}
	
	&:disabled {
    cursor: not-allowed;
    filter: grayscale(1);
	}
`

const Arrow = styled.span`
  -webkit-transition: all ease-in-out 150ms;
  -moz-transition: all ease-in-out 150ms;
  -o-transition: all ease-in-out 150ms;
  transition: all ease-in-out 150ms;
	
	svg {
    fill: ${props => props.color || "var(--note-dropdown-primary-color)"};
    top: 0;
    width: 8px;
    height: 8px;
    margin-left: 3px;
	}
	
	${props => (props.rotate === "true") && css`
    -moz-transform: rotate(60deg);
    -webkit-transform: rotate(60deg);
    -o-transform: rotate(60deg);
    transform: rotate(60deg);
	`}
`

const List = styled.ul`
  width: 100%;
  max-height: 140px;
  overflow-y: auto;
  padding: 0;
  position: absolute;
  margin: 8px 0;
  left: 0;
  background-color: ${props => props.backgroundColor || "var(--note-dropdown-background-color)"};
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  display: flex;
  //justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  color: ${props => props.color || "var(--note-dropdown-primary-color)"};
  z-index: ${props => props.zIndex || "var(--note-dropdown-list-z-index)"};
  
  ${props => props.pos === "bottom" && css`
    top: auto;
  `}

  ${props => props.pos === "top" && css`
    bottom: 14px;
  `}

  &[hidden] {
    display: none;
  }
	
	&[hidden=false] {
    display: flex;
	}
	
	li[role=option]:first-child {
    border-radius: 8px 8px 0 0;
	}
	
	li[role=option]:last-child {
    border-radius:  0 0 8px 8px;
	}
`

const ListOption = styled.li`
  width: 100%;
  text-align: center;
  padding: 5px 0;
	
	&:hover {
    filter: grayscale(1);
    cursor: pointer;
    background-color: ${props => props.hoverColor || "var(--note-dropdown-background-hover-color)"}};
	}

 &[aria-selected=true] {
   border: 2px solid ${props => props.color || "var(--note-dropdown-primary-color)"} !important;
 }
`

const ContainerNoteBadge = styled.div`
  padding: 0 10px;
`

const NoteBadge = styled.span`
  background-color: ${props => props.color || "var(--note-dropdown-primary-color)"};
  border-radius: 10px;
  height: 20px;
  min-width: 20px;
  font-size: 12px;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${props => props.backgroundColor || "var(--note-dropdown-background-color)"};
  padding: 4px;
`

export {
	Container,
	Dropdown,
	DropdownButton,
	Arrow,
	List,
	ListOption,
	ContainerNoteBadge,
	NoteBadge,
}