import React, {Component} from 'react';
import Navigation from "../../fragments/element-navigation/navigation";
import classNameFn from "../../utils/classNameFn";
import QuizCard from "../../fragments/element-quiz-card/quiz-card";
import ElementQuizQuestion from "../../fragments/element-quiz-question/element-quiz-question";
import QuizFinishResult from "../../fragments/element-quiz-finish-result/quiz-finish-result";
import questionIsCorrect from "../../utils/questionIsCorrect";
import * as S from "./element-quiz-6.styled";
import jwtDecode from "jwt-decode";
import API from "../../services";
import {VisibleElement} from "../../utils/visible-element";
import {StringFormat} from "../../utils/string-format";
import {isAdminReviewer} from "../../utils/permission";
import ReviewModal from "./review-modal";
import userCanRemakeQuiz from "../../utils/userCanRemakeQuiz";

export default class ElementQuiz6 extends Component {
  elementRef = React.createRef();
  state = {
    content: this.props.content,
    contentBkp: this.props.content,
    activeChoices: [],
    answeredQuestionsIds: [],
    currentQuestionIndex: 0,
    showResult: false,
    protocol: null,
    custom_options: window.cstmptns || {},
    attempsMade: 0,
    user: {
      roles: [],
    },
    is_review_modal_open: false,
    is_loading_review_modal: false,
    interaction_objects: [],
  }

  componentDidMount() {
    const {content, defaultData} = this.props;

    if (
      defaultData &&
      defaultData.elements &&
      defaultData.elements[content.id]
    ) {
      this.getSignedUserFromJwt()
        .then(this.loadContentFromDefaultData);
    }

    this.getSignedUserFromJwt()
      .then(this.loadContentFromServer);

    this.sortQuestions();
  }

  componentDidUpdate(prevProps, prevStates) {
    if (prevProps.defaultData !== this.props.defaultData) {
      this.loadContentFromDefaultData();
      this.trackElementView();
    }
  }

  sortQuestions(isRemake = false) {
    const contentCopy = { ...this.props.content };
    var splitQuestions = contentCopy.number_of_questions_to_display !== undefined && contentCopy.number_of_questions_to_display > 0;
    var randomizeOnRemake = contentCopy.random_on_remake !== undefined && contentCopy.random_on_remake.enabled === true;
    var sortQuestions = contentCopy.random_question_order !== undefined && contentCopy.random_question_order.enabled === true;

    if ((randomizeOnRemake && isRemake) || !isRemake) {
      const backup = this.props.content.questions;
      var newQuestions = this.props.content.questions;

      if (sortQuestions) {
        newQuestions.sort((a, b) => {
          return Math.floor(Math.random() * (1 - -1)) + -1;
        });
      }

      if (splitQuestions) {
        newQuestions = newQuestions.slice(0, contentCopy.number_of_questions_to_display);
      }

      contentCopy.questions = newQuestions;

      this.props.content.questions = backup;
      this.setState({
        content: contentCopy
      })
    }

    return contentCopy;
  }

  getSignedUserFromJwt = async () => {
    if (!this.state.custom_options) return;
    const {custom_options: {access_token}} = this.state;

    return new Promise((resolve, reject) => {
      let user = jwtDecode(access_token);

      if (user) {
        let roles = [];
        if (user.role && Array.isArray(user.role)) {
          roles = user.role.map(role => role.toString().toLowerCase())
        } else if (user.role && typeof user.role === "string") {
          roles.push(user.role.toString().toLowerCase())
        }
        user = {...user, roles, uid: user.oid || ""};
        this.setState({
          user
        }, () => {
          resolve(user);
        });
      } else {
        reject("Error jwt decode");
      }
    });
  }

  loadContentFromServer = () => {
    this.recoverObjects()
      .then(this.updateQuestionsBasedOnObjects);
  }

  loadContentFromDefaultData = (defaultData) => {
    try {
      const {content} = this.props;

      if (defaultData && defaultData.elements) {
        let cloneContent = content;
        let loadActiveChoices;

        loadActiveChoices = defaultData.elements[content.id].answers
          ? defaultData.elements[content.id].answers
          : [];

        this.setState({
          content: cloneContent,
          activeChoices: loadActiveChoices,
        });
      }
    } catch (error) {
      console.error(error);
    }
  }

  recoverObjects = async () => {
    const {
      content,
    } = this.props;
    const {
      custom_options,
    } = this.state;
    const {
      activity_uid
    } = custom_options;

    if (custom_options === undefined) return;

    const { interaction_uid } = custom_options;

    return await new Promise(async (resolve, reject) => {
      if (content) {
        const objects = await API.cms.learning.interaction
          .uid(interaction_uid)
          .recoverObject({
            context_uid: activity_uid,
          }, custom_options);

        if (objects && objects.length > 0) {
          resolve(objects);
        }
      }

      reject();
    })
  }

  updateQuestionsBasedOnObjects = async (objects = []) => {
    if (objects.length > 0) {
      const {
        content,
      } = this.state;
      const {
        questions = []
      } = content;
      let attempsMade = 0;
      let answeredQuestionsIds = [];

      content.questions.forEach((question) => {
        let lastObjectDate = null;
        const objectTries = objects.filter((object) => {
          return object.target_uid === question.id;
        }).map(object => {

          if (lastObjectDate === null) {
            lastObjectDate = object.created_at;
          } else {
            if (object.created_at > lastObjectDate) {
              lastObjectDate = object.created_at;
            }
          }

          return object;
        });

        attempsMade = objectTries.length;

        const object = objectTries.find((obj) => {
          return obj.created_at >= lastObjectDate;
        });

        if (object) {
          answeredQuestionsIds.push(question.id);
          const answer = object.object_data;

          if (answer && answer.data) {
            this.updateActiveChoicesBasedOnAnswer(question, answer.data);
          }
        }
      });

      let currentQuestionIndex = answeredQuestionsIds.length - 1;
      this.setState({
        attempsMade,
        currentQuestionIndex: currentQuestionIndex >= 0 ? currentQuestionIndex : 0,
        answered_qtd: answeredQuestionsIds.length,
        answeredQuestionsIds,
      }, () => {
        if (answeredQuestionsIds.length === questions.length) {
          this.showResult();
        }
      });
    }

    return objects;
  }

  updateActiveChoicesBasedOnAnswer = (question, answer) => {
    let activeChoices = {...this.state.activeChoices};

    switch (question.type) {
      case "quiz-1":
        question.choices.forEach((choice) => {
          if (answer.id === choice.id) {
            activeChoices[question.id] = choice.id;
          }
        });
        break;
      case "quiz-2":
        activeChoices[question.id] = [];
        question.choices.forEach((choice) => {
          if (answer.find(c => c.id === choice.id) !== undefined) {
            activeChoices[question.id].push(choice.id);
          }
        });
        break;
      case "quiz-5":
        activeChoices[question.id] = answer;
        break;
      default:
        console.error("Question type not found");

    }

    this.setState({
      activeChoices,
    });
  }

  getUsersInteractions = () => {
    const {
      content,
      custom_options,
    } = this.state;
    const {
      register_uid,
      activity_uid,
    } = custom_options;

    return API.cms
      .learning
      .register.uid(register_uid)
      .interaction
      .recoverObjects({context_uid: activity_uid, activity_uid, last_occurrence: true}, custom_options);
  }

  //region Trackers
  trackElementView = () => {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          action: "view",
          element: "quiz-mixed",
          timestamp: Math.floor(Date.now() / 1000),
          metadata: {
            composition_uid: window.structure.metadata.content_uid,
            element_id: content.id,
            element_title: content.content_title,
          },
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        }

        this.props.onTrackEvent(obj);
        this.trackQuestionView();
      }
      this.setState({
        visible: isVisible,
      });
    }
  }

  trackQuestionView = () => {
    var content = this.props.content;
    var currentQuestion = content.questions[this.state.currentQuestionIndex];
    var obj = {
      action: "view",
      element: "quiz-mixed",
      verb: "viewed",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: currentQuestion.id,
          type: currentQuestion.type,
          position: this.state.currentQuestionIndex + 1,
          title: StringFormat.HtmlToText(currentQuestion.question),
        },
      },
    };
    this.props.onTrackEvent(obj);
  }

  trackAnswerClick = (choice) => {
    var content = this.props.content;
    var currentQuestion = content.questions[this.state.currentQuestionIndex];
    var obj = {
      action: "click",
      verb: "focused",
      element: "quiz-mixed-question-answer",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: currentQuestion.id,
          type: currentQuestion.type,
          position: this.state.currentQuestionIndex + 1,
          title: StringFormat.HtmlToText(currentQuestion.question),
          answer: choice,
        },
      },
    };

    if (currentQuestion.type === "quiz-1" || currentQuestion.type === "quiz-2") {
      obj.metadata.question.answer = {
        id: choice.id,
        title: StringFormat.HtmlToText(choice.text),
      };
    }

    this.props.onTrackEvent(obj);
  }

  trackAnswerConfirmClick = (questionId, questions) => {
    var content = this.props.content;
    const {activeChoices, currentQuestionIndex} = this.state;

    let question = questions[currentQuestionIndex] || null;
    let questionIndex = currentQuestionIndex || -1;

    if (question !== null) {
      let selectedChoice;
      let correctAnswer = null;
      let answer;

      switch (question.type) {
        case "quiz-1":
          selectedChoice = question.choices.find((choice) => activeChoices[question.id] === choice.id);
          correctAnswer = questionIsCorrect(question, activeChoices);
          answer = {
            id: selectedChoice.id,
            title: StringFormat.HtmlToText(selectedChoice.text),
            correct: correctAnswer,
          };
          break;
        case "quiz-2":
          selectedChoice = question.choices.filter((choice) => activeChoices[question.id].includes(choice.id));
          correctAnswer = questionIsCorrect(question, activeChoices);
          answer = selectedChoice.map((choice) => ({
            id: choice.id,
            title: StringFormat.HtmlToText(choice.text),
            correct: correctAnswer,
          }));
          break;
        case "quiz-5":
          answer = activeChoices[question.id];
          break;
          default:
            console.error("Question type not supported: ", question.type);
      }


      const obj = {
        action: "click",
        element: "quiz-mixed-question-confirm-answer",
        verb: "answered",
        timestamp: Math.floor(Date.now() / 1000),
        metadata: {
          composition_uid: window.structure.metadata.content_uid,
          element_id: content.id,
          element_title: content.content_title,
          total_questions: questions.length,
          current_question: questionIndex,
          question: {
            id: question.id,
            type: question.type,
            position: this.state.currentQuestionIndex + 1,
            title: StringFormat.HtmlToText(question.question),
            answer,
            answers: question.choices ? question.choices.map(choice => {
              let result = { ... choice };
              result.text = StringFormat.HtmlToText(result.text);
              return result;
            }) : null
          },
        },
      };

      this.props.onTrackEvent(obj);
      this.trackQuestionFeedback(correctAnswer, answer, question);
    }
  }

  trackQuestionResult = (correctAnswer, selectedChoice, question) => {
    var content = this.props.content;
    var obj = {
      action: "result",
      verb: "viewed",
      element: "quiz-mixed-question-result",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: question.id,
          type: question.type,
          position: this.state.currentQuestionIndex + 1,
          title: StringFormat.HtmlToText(question.question),
          answer: {
            id: selectedChoice.id,
            title: StringFormat.HtmlToText(selectedChoice.text),
            hit: correctAnswer,
          },
        },
      },
    };

    this.props.onTrackEvent(obj);
  }

  trackQuestionFeedback = (correctAnswer, answer, question) => {
    if (question.no_feedback === false) {
      var content = this.props.content;
      var obj = {
        action: "view",
        element: "quiz-mixed-question-feedback",
        timestamp: Math.floor(Date.now() / 1000),
        metadata: {
          composition_uid: window.structure.metadata.content_uid,
          element_id: content.id,
          element_title: content.content_title,
          question: {
            id: question.id,
            type: question.type,
            position: this.state.currentQuestionIndex + 1,
            title: StringFormat.HtmlToText(question.question),
            answer,
          },
        },
      };
      this.props.onTrackEvent(obj);
    }
  }

  trackNavigation = (direction) => {
    var content = this.props.content;
    var element =
      direction === "next"
        ? "quiz-mixed-question-navigation-next"
        : "quiz-mixed-question-navigation-previous";
    var source =
      direction === "next"
        ? this.state.currentQuestionIndex - 1
        : this.state.currentQuestionIndex + 1;
    var source_question = this.props.content.questions[source];
    var question = this.props.content.questions[this.state.currentQuestionIndex];

    var obj = {
      action: "click",
      element: element,
      timestamp: Math.floor(Date.now() / 1000),
      verb: direction === "next" ? "advanced" : "retreated",
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: question.id,
          type: question.type,
          position: this.state.currentQuestionIndex + 1,
          title: StringFormat.HtmlToText(question.question),
        },
        source_question: {
          id: source_question.id,
          position:
            (direction === "next"
              ? this.state.currentQuestionIndex - 1
              : this.state.currentQuestionIndex + 1) + 1,
          title: StringFormat.HtmlToText(source_question.question),
        },
      },
    };
    this.props.onTrackEvent(obj);
  }

  trackQuizFinishView = (resume) => {
    var content = this.props.content;
    var obj = {
      action: "view",
      element: "quiz-mixed-feedback",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        resume,
      },
    };
    this.props.onTrackEvent(obj);
  }

  trackQuizResult(questions, resume) {
    let evaluative = false;

    for (let i = 0; i < questions.length; i++) {
      if (!questions[i].no_correct_answer) {
        evaluative = true;
        break;
      }
    }

    let content = this.props.content;
    let obj = {
      action: "view",
      verb: "viewed",
      element: "quiz-mixed-result",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        questions: questions,
      },
    };

    if (evaluative === true) {
      obj.metadata.result = {
        hits: resume.rdp_html_content.hits,
        total: resume.rdp_html_content.total,
        percentage_average:
          (100 / resume.rdp_html_content.total) * resume.rdp_html_content.hits,
        min_percentage: resume.rdp_html_content.min_percentage,
      };
    }

    this.props.onTrackEvent(obj);
  }

  trackRemakeQuiz = () => {
    var content = this.props.content;
    var obj = {
      action: "click",
      element: "quiz-mixed-remake",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
      },
    };
    this.props.onTrackEvent(obj);
  }
  //endregion Trackers

  getLabels = () => {
    const {
      content: {
        questions = []
      }
    } = this.state;
    const {
      contentLanguage = "pt_br"
    } = this.props;

    const labels = {
      en: {
        confirm: "Confirm",
        question: "Question",
        back: "Back",
        retry: "Try again",
        previous: "Previous",
        next: "Next",
        finish: questions.length > 1 ? "Send answers" : "Send answer",
        incorrect: "Incorrect",
        right: "Right!",
        finish_quiz_message: "You have completed this quiz.",
        edit: "Edit",
        answer_saved: "Answer saved!",
        reviewed: "Reviewed",
        review: "Review answers",
      },
      pt_br: {
        confirm: "Confirmar",
        question: "Questão",
        retry: "Refazer atividade",
        previous: "Anterior",
        next: "Próximo",
        finish: questions.length > 1 ? "Enviar respostas" : "Enviar resposta",
        incorrect: "Incorreto",
        right: "Correto!",
        finish_quiz_message: "Você finalizou este questionário.",
        edit: "Editar resposta",
        answer_saved: "Resposta salva!",
        review: "Revisar respostas",
        reviewed: "Revisado",
      }
    }

    return labels[contentLanguage] || labels["en"];
  }

  isFinishButtonVisible = () => {
    const {currentQuestionIndex, content} = this.state;
    const {questions} = content;
    let current = currentQuestionIndex + 1;
    let questions_count = questions.length;

    return current === questions_count;
  }

  isFinishButtonDisabled = () => {
    const {content, answeredQuestionsIds} = this.state;
    const {questions} = content;
    let answeredQuestions = questions.filter(q => answeredQuestionsIds.includes(q.id));

    return questions.length !== answeredQuestions.length;
  }

  updateContentAndNotifyChange = (content) => {
    this.setState({
      content
    }, () => {
      this.props.onChange(this.state.content)
    });
  }

  setAnswer = (questionId, choice) => {
    let {content, activeChoices} = this.state;
    let {questions} = content;
    let question = questions.find(q => q.id === questionId);

    switch (question.type) {
      case "quiz-1":
        this.setState({activeChoices: {...activeChoices, [questionId]: choice.id}});
        break;

      case "quiz-2":
        if (activeChoices[question.id]) {
          if (activeChoices[question.id].includes(choice.id)) {
            activeChoices[question.id] = activeChoices[question.id].filter(selected => selected !== choice.id);
          } else {
            activeChoices[question.id].push(choice.id);
          }
        } else {
          activeChoices[question.id] = [];
          activeChoices[question.id].push(choice.id);
        }

        this.setState({
          activeChoices: activeChoices
        });
        break;
      case "quiz-3":
      case "quiz-4":
      case "quiz-5":
        this.setState({activeChoices: {...activeChoices, [questionId]: choice}});
        break;
      default:
        return;
    }

    this.trackAnswerClick(choice);
  }

  setAnswerConfirmation = (questionId) => {
    const {answeredQuestionsIds, content: {
      questions
    }} = this.state;
    this.setState({
      answeredQuestionsIds: [...answeredQuestionsIds, questionId]
    }, () => {
      this.trackAnswerConfirmClick(questionId, questions)
    })
  }

  deleteAnswerConfirmation = (questionId) => {
    const {answeredQuestionsIds} = this.state;
    this.setState({
      answeredQuestionsIds: answeredQuestionsIds.filter(id => id !== questionId)
    })
  }

  getContentOnlyWithQuestion = (question) => {
    const {
      content,
    } = this.state;

    let contentCopy = {...content};
    contentCopy.questions = [question];

    return contentCopy;
  }

  goToPreviusQuestion = () => {
    const {currentQuestionIndex} = this.state;
    this.setState({
      currentQuestionIndex: currentQuestionIndex - 1
    }, () => {
      this.trackQuestionView();
      this.trackNavigation("prev");
    });
  }

  goToNextQuestion = () => {
    const {currentQuestionIndex} = this.state;
    this.setState({
      currentQuestionIndex: currentQuestionIndex + 1
    }, () => {
      this.trackQuestionView();
      this.trackNavigation("next");
    });
  }

  createInteractionObject = async (question) => {
    const {
      content,
      custom_options,
      activeChoices,
    } = this.state;

    let object_data = null;

    switch (question.type) {
      case "quiz-1":
        object_data = question.choices.find(c => c.id === activeChoices[question.id]);
        break;
      case "quiz-2":
        object_data = question.choices.filter(c => activeChoices[question.id].includes(c.id));
        break;
      case "quiz-5":
        object_data = activeChoices[question.id];
        break;
      default:
        console.error("Invalid question type");
    }

    if (custom_options) {
      const {
        interaction_uid,
        activity_uid
      } = custom_options;
      return API.cms.learning.interaction
        .uid(interaction_uid)
        .createObject({
          context_uid: activity_uid,
          type: content.type,
          target_uid: question.id,
          object_data,
        }, custom_options);
    }
  }

  showResult = async () => {
    const {content, activeChoices, answeredQuestionsIds, attempsMade} = this.state;
    const {questions} = content;
    let hits = 0;
    const quizTypesWithReview = ["quiz-5"];
    const questionsPendingReview = questions.filter(q => quizTypesWithReview.includes(q.type));
    let total = questions.length - questionsPendingReview.length;

    questions
      .filter(q => quizTypesWithReview.includes(q.type) === false)
      .forEach(async question => {
        if (answeredQuestionsIds.includes(question.id)) {
          if (questionIsCorrect(question, activeChoices)) {
            hits++;
          }
        }
      });

    const resume = {
      rdp_html_content: {
        questions: questions,
        hits: hits,
        total: total,
        pending_review_total: questionsPendingReview.length,
        percentage_average: (100 / total * hits)
      }
    };

    this.setState({
      grades: resume,
      showResult: true,
      attempsMade: attempsMade + 1,
    }, () => {
      this.trackQuizResult(questions, resume);
    });
  }

  onFinishClick = async () => {
    const {content} = this.state;
    const {questions} = content;

    const promises = questions.map(async question => await this.createInteractionObject(question));

    await Promise.all(promises)

    this.showResult();
  }

  handleCloseReviewModal = () => {
    this.setState({
      is_review_modal_open: false,
    });
  }

  startReviewForQuestion = (question) => {
    this.getUsersInteractions()
      .then(interaction_objects => {
        this.setState({
          is_review_modal_open: true,
          is_loading_review_modal: false,
          current_question: question,
          interaction_objects
        })
      })
      .catch(error => {
        console.error("Error getting interaction objects", error);
      });
  }

  getNoteRange = () => {
    const {
      content: {
        grade_type = "numeral",
        grade_numeral = {
          min: 0,
          max: 10,
          steps: 1,
        },
      }
    } = this.state;
    const {min = 0, max = 10, steps = 1} = grade_numeral;

    switch (grade_type) {
      case 'numeral':
      default:
        let values = [];

        for (let i = min; i <= max; i += steps) {
          values.push(i);
        }

        return values;
    }
  }

  renderReview(question) {
    const {
      content: {
        primary_color
      },
      is_loading_review_modal
    } = this.state;

    return (
      <div>
        <div className="rdp-react-quiz-5-stats-container">
          <div className="rdp-react-quiz-5-stats-container-content">
            <S.OutlinedButton
              tabIndex={1}
              type="button"
              onClick={() => {
                this.setState({
                  is_loading_review_modal: true,
                }, () => this.startReviewForQuestion(question))
              }}
              background={primary_color}
              disabled={is_loading_review_modal}
            >
              {this.getLabels().review}...
            </S.OutlinedButton>
          </div>
        </div>
      </div>
    )
  }

  resetState = () => {
    this.setState({
      answered: false,
      grades: null,
      showResult: false,
      currentQuestionIndex: 0,
      activeChoices: [],
      answeredQuestionsIds: [],
    })
    this.sortQuestions(true);
  }

  handleRemakeQuiz = () => {
    this.resetState();
    this.trackRemakeQuiz();
  }

  getFont = (font) => {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  getQuestionsByType = (type) => {
    const {content} = this.state;
    return content.questions.filter(q => q.type === type);
  }

  renderContentFor = (currentQuestionIndex) => {
    const {
      contentLanguage,
      headerActionsTemplate,
      footerActionsTemplate,
    } = this.props;
    const {
      content,
      showResult,
      activeChoices,
      answeredQuestionsIds,
      grades,
      user: {
        roles = []
      },
      custom_options,
      is_review_modal_open,
      interaction_objects,
      attempsMade,
    } = this.state;
    const {
      questions,
      primary_color,
      confirm_button_text_color
    } = content;

    if (showResult) {
      return (
        <QuizFinishResult
          grades={grades}
          labels={this.getLabels()}
          canRemake={userCanRemakeQuiz(content, attempsMade)}
          onClickRemake={this.handleRemakeQuiz}
          content={content}
          contentLanguage={contentLanguage}
        />
      )
    } else if (questions) {
      let question = questions[currentQuestionIndex];

      return (
        <>
          <ElementQuizQuestion
            data={question}
            answered={answeredQuestionsIds.includes(question.id)}
            labels={this.getLabels()}
            content={this.getContentOnlyWithQuestion(question)}
            questionNumber={currentQuestionIndex}
            questionTotal={questions.length}
            contentLanguage={contentLanguage}
            headerActionsTemplate={headerActionsTemplate}
            footerActionsTemplate={footerActionsTemplate}
            activeChoices={activeChoices}
            onAnswering={this.setAnswer}
            onConfirmAnswer={this.setAnswerConfirmation}
            onResetAnswer={this.deleteAnswerConfirmation}
            onUpdateContent={this.updateContentAndNotifyChange}
            onlyShowQuestion={isAdminReviewer(roles)}
          />

          {(isAdminReviewer(roles)) && this.renderReview(question)}

          <Navigation
            showFinishButton={this.isFinishButtonVisible()}
            disableFinishButton={this.isFinishButtonDisabled()}
            currentProgress={currentQuestionIndex + 1}
            totalProgress={questions.length}
            primaryColor={primary_color}
            labels={this.getLabels()}
            activeChoices={activeChoices}
            onClickPrevius={this.goToPreviusQuestion}
            onClickNext={this.goToNextQuestion}
            onClickFinish={this.onFinishClick}
          />

          {
            (isAdminReviewer(roles)) && (
              <ReviewModal
                open={is_review_modal_open}
                allQuestions={questions}
                content={content}
                data={this.getQuestionsByType("quiz-5")}
                customOptions={custom_options}
                users={interaction_objects}
                btnConfirmTextColor={confirm_button_text_color}
                primaryColor={primary_color}
                noteRange={this.getNoteRange()}
                onCancel={this.handleCloseReviewModal}
                onConfirm={this.handleCloseReviewModal}
                labels={this.getLabels()}
                updateLocalStudentInteraction={(interaction, selectedUser) => {
                  let studentsCopy = [...interaction_objects];

                  studentsCopy = studentsCopy.map(student => {
                    if (student.user_uid === selectedUser.uid) {
                      if (student.interaction_objects.length === 0) {
                        student.interaction_objects = [interaction];
                      } else {
                        student.interaction_objects = student.interaction_objects.map(row => {
                          if (row.uid === interaction.uid) {
                            return {
                              ...row,
                              score: interaction.score,
                              reviewed_at: interaction.reviewed_at,
                              reviewer_user_uid: interaction.reviewer_user_uid,
                              updated_at: interaction.updated_at,
                            };
                          }
                          return row;
                        });
                      }
                    }
                    return student;
                  });

                  this.setState({
                    interaction_objects: studentsCopy,
                  })
                }}
              />
            )
          }
        </>
      )
    }

    return null;
  }

  render() {
    const {
      headerActionsTemplate,
      footerActionsTemplate,
      content,
      className
    } = this.props;
    const {
      currentQuestionIndex,
    } = this.state;

    return (
      <S.ElementQuizContainer
        ref={this.elementRef}
        className={classNameFn([
          className,
          this.getFont(content.font_family) || ""
        ])}
        style={{
          backgroundColor: content.background_color,
          paddingTop: content.padding_top + "px",
          paddingBottom: content.padding_bottom + "px"
        }}
      >
        {headerActionsTemplate}

        <QuizCard>
          {this.renderContentFor(currentQuestionIndex)}
        </QuizCard>

        {footerActionsTemplate}
      </S.ElementQuizContainer>
    )
  }

}
