import React, {Component} from 'react';
import Navigation from "../../fragments/element-navigation/navigation";
import classNameFn from "../../utils/classNameFn";
import QuizCard from "../../fragments/element-quiz-card/quiz-card";
import ElementQuizQuestion from "../../fragments/element-quiz-question/element-quiz-question";
import QuizFinishResult from "../../fragments/element-quiz-finish-result/quiz-finish-result";
import * as S from "./element-quiz-4.styled";
import {VisibleElement} from "../../utils/visible-element";
import {StringFormat} from "../../utils/string-format";
import jwtDecode from "jwt-decode";
import API from "../../services";
import questionIsCorrect from "../../utils/questionIsCorrect";

export default class ElementQuiz4 extends Component {
  elementRef = React.createRef();
  state = {
    content: this.props.content,
    contentBkp: this.props.content,
    activeChoices: [],
    answeredQuestionsIds: [],
    currentQuestionIndex: 0,
    showResult: false,
    visible: false,
    protocol: null,
    custom_options: window.cstmptns || {},
    attempsMade: 0,
    user: {
      roles: [],
    },
    interaction_objects: [],
  }

  componentDidMount() {
    const {content, defaultData} = this.props;

    if (
      defaultData &&
      defaultData.elements &&
      defaultData.elements[content.id]
    ) {
      this.getSignedUserFromJwt()
        .then(this.loadContentFromDefaultData);
    }

    this.getSignedUserFromJwt()
      .then(this.loadContentFromServer);

    this.sortQuestions();
  }

  componentDidUpdate(prevProps, prevStates) {
    this.trackElementView();
  }

  sortQuestions(isRemake = false) {
    const contentCopy = { ...this.props.content };
    var splitQuestions = contentCopy.number_of_questions_to_display !== undefined && contentCopy.number_of_questions_to_display > 0;
    var randomizeOnRemake = contentCopy.random_on_remake !== undefined && contentCopy.random_on_remake.enabled === true;
    var sortQuestions = contentCopy.random_question_order !== undefined && contentCopy.random_question_order.enabled === true;

    if ((randomizeOnRemake && isRemake) || !isRemake) {
      const backup = this.props.content.questions;
      var newQuestions = this.props.content.questions;

      if (sortQuestions) {
        newQuestions.sort((a, b) => {
          return Math.floor(Math.random() * (1 - -1)) + -1;
        });
      }

      if (splitQuestions) {
        newQuestions = newQuestions.slice(0, contentCopy.number_of_questions_to_display);
      }

      contentCopy.questions = newQuestions;

      this.props.content.questions = backup;
      this.setState({
        content: contentCopy
      })
    }

    return contentCopy;
  }

  getSignedUserFromJwt = async () => {
    if (!this.state.custom_options) return;
    const {custom_options: {access_token}} = this.state;

    return new Promise((resolve, reject) => {
      let user = jwtDecode(access_token);

      if (user) {
        let roles = [];
        if (user.role && Array.isArray(user.role)) {
          roles = user.role.map(role => role.toString().toLowerCase())
        } else if (user.role && typeof user.role === "string") {
          roles.push(user.role.toString().toLowerCase())
        }
        user = {...user, roles, uid: user.oid || ""};
        this.setState({
          user
        }, () => {
          resolve(user);
        });
      } else {
        reject("Error jwt decode");
      }
    });
  }

  loadContentFromServer = () => {
    this.recoverObjects()
      .then(this.updateQuestionsBasedOnObjects);
  }

  loadContentFromDefaultData = (defaultData) => {
    try {
      const {content} = this.props;

      if (defaultData && defaultData.elements) {
        let cloneContent = content;
        let loadActiveChoices;

        loadActiveChoices = defaultData.elements[content.id].answers
          ? defaultData.elements[content.id].answers
          : [];

        this.setState({
          content: cloneContent,
          activeChoices: loadActiveChoices,
        });
      }
    } catch (error) {
      console.error(error);
    }
  }

  recoverObjects = async () => {
    const {
      content,
    } = this.props;
    const {
      custom_options,
    } = this.state;
    const {
      activity_uid
    } = custom_options;

    if (custom_options === undefined) return;

    const { interaction_uid } = custom_options;

    return await new Promise(async (resolve, reject) => {
      if (content) {
        const objects = await API.cms.learning.interaction
          .uid(interaction_uid)
          .recoverObject({
            context_uid: activity_uid,
          }, custom_options);

        if (objects && objects.length > 0) {
          resolve(objects);
        }
      }

      reject();
    })
  }

  updateQuestionsBasedOnObjects = async (objects = []) => {
    if (objects.length > 0) {
      const {
        content,
      } = this.state;
      const {
        questions = []
      } = content;
      let attempsMade = 0;
      let answeredQuestionsIds = [];

      content.questions.forEach((question) => {
        let lastObjectDate = null;
        const objectTries = objects.filter((object) => {
          return object.target_uid === question.id;
        }).map(object => {

          if (lastObjectDate === null) {
            lastObjectDate = object.created_at;
          } else {
            if (object.created_at > lastObjectDate) {
              lastObjectDate = object.created_at;
            }
          }

          return object;
        });

        attempsMade = objectTries.length;

        const object = objectTries.find((obj) => {
          return obj.created_at >= lastObjectDate;
        });

        if (object) {
          answeredQuestionsIds.push(question.id);
          const answer = object.object_data;

          if (answer && answer.data) {
            this.updateActiveChoicesBasedOnAnswer(question, answer.data);
          }
        }
      });

      let currentQuestionIndex = answeredQuestionsIds.length - 1;
      this.setState({
        attempsMade,
        currentQuestionIndex: currentQuestionIndex >= 0 ? currentQuestionIndex : 0,
        answered_qtd: answeredQuestionsIds.length,
        answeredQuestionsIds,
      }, () => {
        if (answeredQuestionsIds.length === questions.length) {
          this.showResult();
        }
      });
    }

    return objects;
  }

  updateActiveChoicesBasedOnAnswer = (question, answer) => {
    let activeChoices = {...this.state.activeChoices};

    activeChoices[question.id] = answer.choices;

    this.setState({
      activeChoices,
    });
  }

  getUsersInteractions = () => {
    const {
      custom_options,
    } = this.state;
    const {
      register_uid,
      activity_uid,
    } = custom_options;

    return API.cms
      .learning
      .register.uid(register_uid)
      .interaction
      .recoverObjects({context_uid: activity_uid, activity_uid, last_occurrence: true}, custom_options);
  }

  trackElementView = () => {
    const {
      content
    } = this.props;
    const isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        const obj = {
          action: "view",
          element: "quiz-match",
          timestamp: Math.floor(Date.now() / 1000),
          metadata: {
            composition_uid: window.structure.metadata.content_uid,
            element_id: content.id,
            element_title: content.content_title,
          },
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        }

        this.props.onTrackEvent(obj);
        this.trackQuestionView();
      }
      this.setState({
        visible: isVisible,
      });
    }
  }

  trackQuestionView = () => {
    const content = this.props.content;
    const currentQuestion = content.questions[this.state.currentQuestionIndex];
    const obj = {
      action: "view",
      element: "quiz-match",
      verb: "viewed",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        question: {
          id: currentQuestion.id,
          position: this.state.currentQuestionIndex + 1,
          title: StringFormat.HtmlToText(currentQuestion.question),
        },
      },
    };
    this.props.onTrackEvent(obj);
  }

  trackQuizFinishView = (resume) => {
    var content = this.props.content;
    var obj = {
      action: "view",
      element: "quiz-match-feedback",
      timestamp: Math.floor(Date.now() / 1000),
      metadata: {
        composition_uid: window.structure.metadata.content_uid,
        element_id: content.id,
        element_title: content.content_title,
        resume,
      },
    };
    this.props.onTrackEvent(obj);
  }

  getLabels = () => {
    const {
      content: {
        questions = []
      }
    } = this.state;
    const {
      contentLanguage = "pt_br"
    } = this.props;

    const labels = {
      en: {
        confirm: "Confirm",
        question: "Question",
        back: "Back",
        retry: "Try again",
        previous: "Previous",
        next: "Next",
        finish: questions.length > 1 ? "Send answers" : "Send answer",
        incorrect: "Incorrect",
        right: "Right!",
        finish_quiz_message: "You have completed this quiz.",
        edit: "Edit",
        answer_saved: "Answer saved!",
      },
      pt_br: {
        confirm: "Confirmar",
        question: "Questão",
        retry: "Refazer atividade",
        previous: "Anterior",
        next: "Próximo",
        finish: questions.length > 1 ? "Enviar respostas" : "Enviar resposta",
        incorrect: "Incorreto",
        right: "Correto!",
        finish_quiz_message: "Você finalizou este questionário.",
        edit: "Editar resposta",
        answer_saved: "Resposta salva!",
      }
    }

    return labels[contentLanguage] || labels["en"];
  }

  isFinishButtonVisible = () => {
    const {currentQuestionIndex, content} = this.state;
    const {questions} = content;
    let current = currentQuestionIndex + 1;
    let questions_count = questions.length;

    return current === questions_count;
  }

  isFinishButtonDisabled = () => {
    const {content, answeredQuestionsIds} = this.state;
    const {questions} = content;
    let answeredQuestions = questions.filter(q => answeredQuestionsIds.includes(q.id));

    return questions.length !== answeredQuestions.length;
  }

  updateContentAndNotifyChange = (content) => {
    this.setState({
      content
    }, () => {
      this.props.onChange(this.state.content)
    });
  }

  setAnswer = (questionId, choice) => {
    let {activeChoices} = this.state;

    this.setState({activeChoices: {...activeChoices, [questionId]: choice}});
  }

  setAnswerConfirmation = (questionId) => {
    const {answeredQuestionsIds} = this.state;
    this.setState({
      answeredQuestionsIds: [...answeredQuestionsIds, questionId]
    })
  }

  deleteAnswerConfirmation = (questionId) => {
    const {answeredQuestionsIds} = this.state;
    this.setState({
      answeredQuestionsIds: answeredQuestionsIds.filter(id => id !== questionId)
    })
  }

  getContentOnlyWithQuestion = (question) => {
    const {
      content,
    } = this.state;

    let contentCopy = {...content};
    contentCopy.questions = [question];

    return contentCopy;
  }

  goToPreviusQuestion = () => {
    const {currentQuestionIndex} = this.state;
    this.setState({
      currentQuestionIndex: currentQuestionIndex - 1
    });
  }

  goToNextQuestion = () => {
    const {currentQuestionIndex} = this.state;
    this.setState({
      currentQuestionIndex: currentQuestionIndex + 1
    });
  }

  renderContentFor = (currentQuestionIndex) => {
    const {
      editable,
      contentLanguage,
      headerActionsTemplate,
      footerActionsTemplate,
    } = this.props;
    const {
      content,
      showResult,
      activeChoices,
      answeredQuestionsIds,
      grades,
    } = this.state;
    const {
      questions,
      primary_color
    } = content;

    if (showResult) {
      return (
        <QuizFinishResult
          grades={grades}
          labels={this.getLabels()}
          onClickRemake={this.resetState}
          content={content}
          contentLanguage={contentLanguage}
        />
      )
    } else if (questions) {
      let question = questions[currentQuestionIndex];
      question.type = "quiz-4";

      return (
        <>
          <ElementQuizQuestion
            data={question}
            answered={answeredQuestionsIds.includes(question.id)}
            labels={this.getLabels()}
            editable={editable}
            content={this.getContentOnlyWithQuestion(question)}
            questionNumber={currentQuestionIndex}
            questionTotal={questions.length}
            contentLanguage={contentLanguage}
            headerActionsTemplate={headerActionsTemplate}
            footerActionsTemplate={footerActionsTemplate}
            activeChoices={activeChoices}
            onAnswering={this.setAnswer}
            onConfirmAnswer={this.setAnswerConfirmation}
            onResetAnswer={this.deleteAnswerConfirmation}
            onUpdateContent={this.updateContentAndNotifyChange}
          />
          <Navigation
            showFinishButton={this.isFinishButtonVisible()}
            disableFinishButton={this.isFinishButtonDisabled()}
            currentProgress={currentQuestionIndex + 1}
            totalProgress={questions.length}
            primaryColor={primary_color}
            labels={this.getLabels()}
            activeChoices={activeChoices}
            onClickPrevius={this.goToPreviusQuestion}
            onClickNext={this.goToNextQuestion}
            onClickFinish={this.onFinishClick}
          />
        </>
      )
    }

    return null;
  }

  createInteractionObject = async (question) => {
    const {
      content,
      custom_options,
      activeChoices,
    } = this.state;

    let object_data = {
      choices: activeChoices[question.id] || [],
      id: null,
    };

    if (custom_options) {
      const {
        interaction_uid,
        activity_uid
      } = custom_options;

      return API.cms.learning.interaction
        .uid(interaction_uid)
        .createObject({
          context_uid: activity_uid,
          type: content.type,
          target_uid: question.id,
          object_data,
        }, custom_options);
    }
  }

  onFinishClick = async () => {
    const {content} = this.state;
    const {questions} = content;

    const promises = questions.map(async question => await this.createInteractionObject(question));

    await Promise.all(promises)

    this.showResult();
  }

  showResult = () => {
    const {
      content,
      activeChoices,
      answeredQuestionsIds,
      attempsMade,
    } = this.state;
    const {questions} = content;
    let hits = 0;
    let total = questions.length;

    questions
      .forEach(question => {
        if (answeredQuestionsIds.includes(question.id)) {
          if (questionIsCorrect(question, activeChoices)) {
            hits++;
          }
        }
      });

    const resume = {
      rdp_html_content: {
        questions: questions,
        hits: hits,
        total: total,
        percentage_average: (100 / total * hits)
      }
    };

    this.setState({
      grades: resume,
      showResult: true,
      attempsMade: attempsMade + 1,
    }, () => {
      this.trackQuizFinishView(resume);
    })
  }

  resetState = () => {
    this.setState({
      answered: false,
      grades: null,
      showResult: false,
      currentQuestionIndex: 0,
      activeChoices: [],
      answeredQuestionsIds: [],
    })
    this.sortQuestions(true);
  }

  getFont = (font) => {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  render() {
    const {
      headerActionsTemplate,
      footerActionsTemplate,
      content,
      className
    } = this.props;
    const {
      currentQuestionIndex,
    } = this.state;

    return (
      <S.ElementQuizContainer
        ref={this.elementRef}
        className={classNameFn([
          className,
          this.getFont(content.font_family) || ""
        ])}
        style={{
          backgroundColor: content.background_color,
          paddingTop: content.padding_top + "px",
          paddingBottom: content.padding_bottom + "px"
        }}
      >
        {headerActionsTemplate}

        <QuizCard>
          {this.renderContentFor(currentQuestionIndex)}
        </QuizCard>

        {footerActionsTemplate}
      </S.ElementQuizContainer>
    )
  }

}
