
const AUTH_API_VERSION = "v1.0";
const PROTOCOL = "https";

export default {
  user: {
    me: ({access_token, api_env, domain, project_key}) => {
      return fetch(`${PROTOCOL}://${domain}/auth/${AUTH_API_VERSION}/user/me`, {
        method: 'GET',
        headers: {
          "Authorization": `Bearer ${access_token}`,
          "Project-Key": project_key,
          "Api-Env": api_env
        }
      }).then(resp => resp.json())
    },
    uid: (uid, {access_token, api_env, domain, project_key}) => {
      return fetch(`${PROTOCOL}://${domain}/auth/${AUTH_API_VERSION}/user/uid/${uid}`, {
        method: 'GET',
        headers: {
          "Authorization": `Bearer ${access_token}`,
          "Project-Key": project_key,
          "Api-Env": api_env
        }
      }).then(resp => resp.json())
    },
  }
}