const userCanRemakeQuiz = (content, attempsMade) => {
  const {
    max_attempts = null,
    remake = "disable"
  } = content;
  // É comparado se o max_attempts é null pois a quantidade de tentativas foi implementada posteriormente,
  // caso a composição não possua esse valor é considerado apenas se ele pode refazer o questionário e ignorado,
  // a verificação do limite de refações
  if (remake === "enable" && String(max_attempts) === 'null') {
    return true;
  }
  if (isNaN(parseInt(max_attempts)) === false && typeof parseInt(max_attempts) === 'number') {
    const default_first_attempt_count = 1;
    const total_attempts = parseInt(max_attempts) + default_first_attempt_count;
    return remake === "enable" && attempsMade < total_attempts
  }

  return false;
}

export default userCanRemakeQuiz;