import React, { Component } from 'react';
import {VisibleElement} from '../../utils/visible-element';
import {StringFormat} from '../../utils/string-format';
import './element-head-1.css';

export default class ElementHead1 extends Component {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();

    this.state = {
      "avatar_size": "width",
      "visible": false
    }
  }

  componentDidMount() {
    this.trackElementView();
  }

  trackElementView() {
    var content = this.props.content;
    var isVisible = VisibleElement.visible(this.elementRef.current);
    if (this.state.visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "header-1",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };

        this.props.onTrackEvent(obj);
      };
      this.setState({
        "visible": isVisible
      });
    };
  }

  handleAvatarChange(img) {
    this.setState({
      "avatar_size": img.target.width >= img.target.height ? "height" : "width"
    });
  }

  renderCategory() {
    var content = this.props.content;

    if (content.show_category === false) return null;

    return <div className="rdp-element-head-1-category" dangerouslySetInnerHTML={{__html: content.category}}/>
  }

  renderAvatar() {
    var content = this.props.content;

    if (content.avatar !== null) {
      var avatarSize = this.state.avatar_size;
      return <div className="rdp-element-head-1-avatar" style={{borderColor: content.avatar_border_color}}>
        <img
          style={avatarSize === "width" ? {width: "102%"} : {height: "102%"}}
          src={content.avatar}
          onLoad={this.handleAvatarChange.bind(this)}
        />
      </div>;
    } else {
      return null;
    }
  }

  renderAuthorInfo() {
    var content = this.props.content;

    if (content.show_author_name === false) return null;

    return <div className="rdp-element-head-1-author-info">
      <div className="rdp-element-head-1-author-name" dangerouslySetInnerHTML={{__html: content.author_name}}/>
      <span className="rdp-element-head-1-author-divider" style={{color: content.primary_color}}> | </span>
      <div className="rdp-element-head-1-author-office" dangerouslySetInnerHTML={{__html: content.author_office}}/>
    </div>
  }

  renderDivider() {
    let { content } = this.props;

    if (content.show_divider === false) return null;

    var secondaryColor = this.props.content.secondary_color;
    return <div className="rdp-element-head-1-divider" style={{backgroundColor: secondaryColor}}></div>
  }

  getFont(font) {
    if (font) return font.replace(/\s/g, '-').toLowerCase();
  }

  render() {
    var content = this.props.content;
    
    return (<div id={content.id} ref={this.elementRef} className={`rdp-element-head-1 ${this.getFont(content.font_family) || ""}`} style={{ backgroundImage: `url(${content.background_image})` }}>
      {this.renderCategory()}
      {this.renderAvatar()}
      {this.renderAuthorInfo()}
      {this.renderDivider()}
    </div>)
  }
}
