import React, { useRef, useState } from "react";
import Slider from "react-slick";
import "animate.css/animate.min.css";

import * as S from "./styles";
import {VisibleElement} from "../../utils/visible-element";

const ElementImagesGallery = ({
  headerActionsTemplate,
  footerActionsTemplate,
  content,
  onTrackEvent
}) => {
  const elementRef = useRef(null);
  const sliderRef = useRef(null);
  const [visible, setVisible] = useState(false);

  React.useEffect(() => {
    trackElementView();
  }, []);

  const trackElementView = () => {
    var isVisible = VisibleElement.visible(elementRef.current);
    if (visible !== isVisible) {
      if (isVisible === true) {
        var obj = {
          "action": "view",
          "element": "images-gallery-1",
          "timestamp": Math.floor(Date.now() / 1000),
          "metadata": {
            "composition_uid": window.structure.metadata.content_uid,
            "element_id": content.id,
            "element_title": content.content_title
          }
        };
        if (content.dam_assets !== undefined && content.dam_assets.length > 0) {
          obj.assets = content.dam_assets;
        };

        onTrackEvent(obj);
      };
      setVisible(isVisible);
    };
  }

  const [isModalActivated, setIsModalActivated] = useState(
    content.images.map(() => false)
  );
  const [currentImageOnModal, setCurrentImageOnModal] = useState(0);

  const handleClickOnImage = (i) => {
    isModalActivated[i] = true;
    setIsModalActivated([...isModalActivated]);
    setCurrentImageOnModal(i);
  };

  const handleCloseModal = () => {
    setIsModalActivated(isModalActivated.map(() => false));
  };

  const handleClickOnNext = () => {
    const newCurrentImage =
      currentImageOnModal === content.images.length - 1
        ? 0
        : currentImageOnModal + 1;

    sliderRef.current.slickGoTo(newCurrentImage);
  };

  const handleClickOnPrev = () => {
    const newCurrentImage =
      currentImageOnModal === 0
        ? content.images.length - 1
        : currentImageOnModal - 1;

    sliderRef.current.slickGoTo(newCurrentImage);
  };

  const handleClickOnDot = (index) => {
    setCurrentImageOnModal(index);
    sliderRef.current.slickGoTo(index);
  };

  const imagesGalleryStyle = {
    backgroundColor: content.background_color,
    paddingTop: content.padding_top + "px",
    paddingBottom: content.padding_bottom + "px",
    color: content.text_color,
  };

  const sliderSettings = {
    dots: false,
    infinite: false,
    arrows: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: currentImageOnModal,
  };

  return (
    <S.ImagesGalleryContainer ref={elementRef} style={imagesGalleryStyle}>
      {headerActionsTemplate}
      <S.ImagesContainer>
        {content.images.map((image, i) => (
          <S.ImageContainer numberOfImages={content.images.length} key={i}>
            <img
              src={image.src}
              alt={image.caption}
              onClick={() => handleClickOnImage(i)}
            />
            <span>{image.caption}</span>
          </S.ImageContainer>
        ))}
      </S.ImagesContainer>
      {isModalActivated.includes(true) && (
        <S.Modal>
          <div>
            <S.ArrowButton onClick={handleClickOnPrev}>
              <S.ArrowLeft />
            </S.ArrowButton>
            <Slider
              ref={sliderRef}
              beforeChange={(currentSlide, nextSlide) =>
                setCurrentImageOnModal(nextSlide)
              }
              {...sliderSettings}
            >
              {content.images.map((image, index) => (
                <div key={index}>
                  <S.ModalImage src={image.src} alt="" />
                  {image.caption && <S.Caption>{image.caption}</S.Caption>}
                </div>
              ))}
            </Slider>
            <S.Dots>
              {content.images.map((image, index) => (
                <S.Dot
                  onClick={() => handleClickOnDot(index)}
                  isSelected={index === currentImageOnModal}
                  key={index}
                />
              ))}
            </S.Dots>
            <S.ArrowButton onClick={handleClickOnNext}>
              <S.ArrowRight />
            </S.ArrowButton>
            <S.Close onClick={handleCloseModal}>&times;</S.Close>
          </div>
        </S.Modal>
      )}
      {footerActionsTemplate}
    </S.ImagesGalleryContainer>
  );
};

export default ElementImagesGallery;
