import React, {PureComponent} from 'react';
import ReactQuill, {Quill as QuillR} from "react-quill";
import 'react-quill/dist/quill.snow.css';
import {GithubPicker} from "react-color";
import {QuillContainer} from "./quill.styled";

class Quill extends PureComponent {
  constructor(props) {
    super(props);

    this.addCustomColorPicker = this.addCustomColorPicker.bind(this);
    this.toggleColor = this.toggleColor.bind(this);
    this.onQuillColorChange = this.onQuillColorChange.bind(this);

    this.quillRef = React.createRef();

    this.state = {
      current_selection: null,
      current_quill_color: "#000",
      render_quill_color_picker: false,
      colors: [
        "#000000", "#555555", "#ABB8C3", "#e60000", "#F47373", "#ff9900", "#FCB900", "#FCCB00",
        "#008a00", "#4CAF50", "#CDDC39", "#0066cc", "#03A9F4", "#8ED1FC", "#9933ff", "#F78DA7", "#ffffff"
      ],
      modules: {
        toolbar: [
          ['bold', 'italic', 'underline','strike'],
          [
            { 'align': '' },
            { 'align': 'center' },
            { 'align': 'right' }
          ],
          [
            { 'script': 'sub' },
            { 'script': 'super' },
            'formula'
          ],
          [
            {
              'size': [
                false, "9px", "10px", "11px", "12px", "13px", "14px",
                "15px", "18px", "20px", "22px", "26px", "30px", "34px",
                "36px", "38px", "40px", "45px", "50px", "55px", "60px"
              ]
            }
          ],
          [{'list': 'ordered'}, {'list': 'bullet'}],
          ['link']
        ]
      },
      formats: [
        'header', 'size', 'bold', 'italic', 'underline', 'strike', 'script',
        'formula', 'list', 'bullet', 'indent', 'link', 'color', 'align'
      ]
    }
  }

  componentWillMount() {
    const Font = QuillR.import('formats/font');
    Font.whitelist = ['large', 'medium', "small", "regular", "bold", "pullquote"];
    QuillR.register(Font, true);

    let SizeStyle = QuillR.import('attributors/style/size');
    SizeStyle.whitelist = [
      "9px", "10px", "11px", "12px", "13px", "14px", "15px", "18px", "20px",
      "22px", "26px", "30px", "34px", "36px", "38px", "40px", "45px", "50px",
      "55px", "60px"
    ]
    QuillR.register(SizeStyle, true);
  }

  componentDidMount() {
    this.addCustomColorPicker();
  }

  addCustomColorPicker() {
    const quillRef = this.quillRef;
    const quill = quillRef.current;

    if (quill !== null) {
      const toolbar = quill.querySelector(".ql-toolbar")
      if (toolbar) {
        toolbar.insertAdjacentHTML("beforeend",
          '<span class="ql-formats"><span class="ql-format-custom-color"></span></span>'
        )
      }
      const customColor = quill.querySelector(".ql-format-custom-color")
      if (customColor) {
        customColor.addEventListener("click", this.toggleColor)
      }
    }
  }

  toggleColor() {
    const {
      render_quill_color_picker
    } = this.state;
    let current_selection = null;
    const selection = window.getSelection();

    if (selection && selection.rangeCount > 0) {
      console.log("range", selection.getRangeAt(0));
      current_selection = selection.getRangeAt(0);
    }

    this.setState({
      current_selection,
      render_quill_color_picker: !render_quill_color_picker
    })
  }

  onQuillColorChange(color) {
    const {
      current_selection
    } = this.state;
    const selection = window.getSelection();
    if (selection && current_selection) {
      selection.removeAllRanges();
      selection.addRange(current_selection);
    }
    this.setState({
      current_quill_color: color.hex,
      render_quill_color_picker: false,
    });

    this.changeSelectedTextColor(color.hex);
  }

  changeSelectedTextColor(color) {
    const quill = this.quillRef;
    const {
      onChange,
    } = this.props;

    if (window.getSelection) {
      const sel = getSelection();
      if (sel.getRangeAt && sel.rangeCount) {
        const range = getSelection().getRangeAt(0);

        let html = '<span style="color: ' + color + ';">' + range + '</span>'
        range.deleteContents();

        let el = document.createElement("div");
        el.innerHTML = html;
        let frag = document.createDocumentFragment(),
          node,
          lastNode;
        while ( (node = el.firstChild) ) {
          lastNode = frag.appendChild(node);
        }
        range.insertNode(frag);
      }
    }

    if (quill.current !== null && onChange) {
      const editor = quill.current.querySelector(".ql-editor");
      if (editor) {
        onChange(editor.innerHTML);
      }
    }
  }

  render() {
    const {
      value,
      onChange,
      placeholder,
    } = this.props;
    const {
      render_quill_color_picker,
      modules,
      formats,
      colors,
      current_quill_color
    } = this.state;

    return (
      <QuillContainer
        ref={this.quillRef}
        quillColor={current_quill_color}
        colorPickerOpened={render_quill_color_picker}
      >
        {
          render_quill_color_picker &&
          <div className="ql-custom-color-picker">
            <div
              className="ql-custom-color-picker-overlay"
              onClick={this.toggleColor}
            />
            <GithubPicker
              colors={colors}
              color={current_quill_color}
              onChange={this.onQuillColorChange}
              triangle="hide"
              width="100%"
            />
          </div>
        }
        <ReactQuill
          value={value}
          onChange={onChange}
          placeholder={placeholder}
          modules={modules}
          formats={formats}
          theme={"snow"}
        />
      </QuillContainer>
    );
  }
}

export default Quill;