import React from 'react';
import styled from "styled-components";
import gif from "./icon-preloader.gif";

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Img = styled.img`
  width: auto;
  height: ${props => props.height};
`;

export default function Preloader({width = "100%", height = "100%", gifHeight = "auto"}) {
  return (
    <Container style={{width: width, height: height}}>
      <Img src={gif} alt="loading..." height={gifHeight}/>
    </Container>
  );
}
