import React from 'react';

export default function withGTM(WrappedComponent) {
  return function HOC(props) {
    const initialize = (container) => {
      if (typeof container === "string" && container.length > 0) {
        const head = document.getElementsByTagName('head')[0];
        const body = document.getElementsByTagName('body')[0];

        window.dataLayer = window.dataLayer || [];

        window.dataLayer.push({
          "gtm.start": new Date().getTime(),
          "event": "gtm.js"
        });

        if (document.getElementById(`gtm-script-${container}`) === null) {
          const script = document.createElement('script');
          script.type = 'text/javascript';
          script.async = true;
          script.src = `https://www.googletagmanager.com/gtm.js?id=${container}`;
          script.setAttribute("id", `gtm-script-${container}`);
          head.insertAdjacentElement("afterbegin", script);
        }

        if (document.getElementById(`gtm-noscript-${container}`) === null) {
          const noscript = document.createElement('noscript');
          const iframe = document.createElement('iframe');
          iframe.src = `https://www.googletagmanager.com/ns.html?id=${container}`;
          iframe.height = "0";
          iframe.width = "0";
          iframe.style.display = "none";
          iframe.style.visibility = "hidden";
          noscript.appendChild(iframe);
          noscript.setAttribute("id", `gtm-noscript-${container}`);
          body.insertAdjacentElement("afterbegin", noscript);
        }
      }
    }

    const addDataLayerEvent = (event) => {
      if (window.dataLayer) {
        window.dataLayer.push({
          ...event,
          event: (event.action ? event.action : "") + (event.verb ? "." + event.verb : "") + (event.element ? "." + event.element : ""),
        });
      }
    }

    return (
      <React.Fragment>
        <WrappedComponent
          {...props}
          _GTM={{
            initialize,
            addDataLayerEvent
          }}
        />
      </React.Fragment>
    );
  };
}